<template>
  <main>
    <div class="modal" :class="{'showform': showForm2}">
        <form class="modal-content">
            <div class="xbar">
                <a href="#"  @click="showForm2 = false, change_pass_error_message = '',old_pass = '', new_pass = '', confirm_pass = '' "><i class='material-icons'>close</i></a> 
             </div>
             <div class="field" style="width:100%;text-align: center;margin-bottom:2rem">
                <p style="color:var(--red)">{{change_pass_error_message}}</p>
             </div>
             <div class="field">
                <p>Old Password</p>
                <input type="password" v-model="old_pass">
             </div>
             <div class="field">
                <p>New Password</p>
                <input type="password" v-model="new_pass">
             </div>
             <div class="field">
                <p>Confirm Password</p>
                <input type="password" v-model="confirm_pass">
             </div>

             <div class="field">
                <button type="button" id="pass_btn" href="#" @click="changePassword()">Submit</button>
             </div>
        </form>
    </div>



 <!--<a href="#" style="margin-bottom:2rem" @click="editProfile = !editProfile"><span v-if="editProfile==false">Edit Profile</span><span v-if="editProfile==true">Cancel</span></a> -->
    <div class="profile-main" v-if="$store.state.usercat=='student' && level != ''">
       
        <table>
            <tr><td>Fullname:</td> <span>{{ $store.state.fullName }}</span></tr>
            <tr><td>Level:</td> <td>{{level}}</td></tr>
            <tr><td>Matric Number:</td> <td>{{$store.state.userdata.username}}</td></tr>
            <tr><td>Department:</td> <td>{{department}}</td></tr>

            <tr><td>Hostel:</td><td><span v-if="editProfile==false">{{hostel}} <span class="hall">Hall</span></span>  
                 <select v-if="editProfile==true" v-model="hostel">
                    <option value="joseph">joseph</option>
                    <option value="joshua">joshua</option>
                 </select>
            </td>
            </tr>

            <tr><td>Phone Number:</td><td> <span v-if="editProfile==false">{{phonenumber}} </span><input v-if="editProfile==true" v-model="phonenumber" type="text">  </td></tr>
            <tr><td>Other Phone Number:</td><td> <span v-if="editProfile==false">{{alternate_number}}</span> <input v-if="editProfile==true" v-model="alternate_number" type="text">  </td></tr>
            <tr><td>Email Address:</td><td>{{$store.state.userdata.email}}</td></tr>
            <tr><td>Home Address:</td><td><span v-if="editProfile==false">{{homeAddress}}</span> <textarea v-if="editProfile==true" v-model="homeAddress" name="" id="" cols="30" rows="10"></textarea></td></tr>
            
        </table>
       
    </div>



    <div class="profile-main" v-if="$store.state.usercat=='parent'">
       
        <table>
            <tr><td>Fullname:</td> <span>{{ $store.state.fullName.split(' ').splice(0,2).join(" ") }}</span></tr>
            

            <tr><td>Phone Number:</td><td> <span v-if="editProfile==false">{{phonenumber}} </span><input v-if="editProfile==true" v-model="phonenumber" type="text">  </td></tr>
            <tr><td>Other Phone Number:</td><td> <span v-if="editProfile==false">{{alternate_number}}</span> <input v-if="editProfile==true" v-model="alternate_number" type="text">  </td></tr>
            <tr><td>Email Address:</td><td>{{$store.state.userdata.email}}</td></tr>
            <tr><td>Home Address:</td><td><span v-if="editProfile==false">{{homeAddress}}</span> <textarea v-if="editProfile==true" v-model="homeAddress" name="" id="" cols="30" rows="10"></textarea></td></tr>
            
        </table>
       
    </div>

    <p href="#" id="saveprofile" v-if="editProfile == true">Update Profile</p>



    <a href="#" v-if="$store.state.usercat == 'student' || $store.state.usercat=='parent' " class="password-button" @click="showForm2 = !showForm2">
        
            <p class="pass-button">Change password</p>
       
    </a>
 
  </main>
</template>

<script>

import axios from 'axios'

import { toast } from 'bulma-toast'
import { loadRouteLocation } from 'vue-router'


export default {
    name:"profile",
    data(){
        return {
            hostel:this.$store.state.hostel,
            userdata:this.$store.state.userdata,
            level:'',
            homeAddress:'',
            alternate_number:this.$store.state.userdata.alternate_number,
            phonenumber:this.$store.state.userdata.phonenumber,
            old_pass:'',
            new_pass:'',
            confirm_pass:'',
            showForm2:false,
            change_pass_error_message:'',
            editProfile:false,
            department:''
        }
    },
    mounted(){
        if(this.$store.state.usercat === 'student'){
            localStorage.setItem('isLoading',true)
        } else {
            localStorage.setItem('isLoading',false)
        }
        console.table(this.userdata)
        this.$store.commit('setCurrentTab','6')
        localStorage.setItem('cT','6')
        if(this.$store.state.usercat==='student'){
            this.getOtherData()
        }
        this.timer4 = setInterval(this.checkAuthStat, 1000)
    },
    beforeUnmount(){
        clearInterval(this.timer4)
    },  
    methods: {
        checkAuthStat() {

            if (this.$store.state.isAuthenticated === false || this.$store.state.token == '') {
                this.logout()
            }
        },
            async logout() {

            const token = this.$store.state.token
            axios.defaults.headers.common["authorization"] = ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")
            localStorage.removeItem('cT')
            this.$store.commit('removeToken')
            await axios
                .post('api/v1/person/logout/', {
                    token: token
                }, )
                .then(response => {
                    toast({
                        message: `Logged out successfully`,
                        type: 'is-info',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 5000,
                        position: 'top-right',
                    })

                })

            this.$router.push('/login')

        },
        async getOtherData(){
            await axios 
                .get(`/api/v1/profile/student/${this.$store.state.username}/`)
                .then(response => {
                    this.level = response.data.level
                    this.homeAddress = response.data.homeAddress
                    this.department = response.data.department
                    localStorage.setItem('isLoading',false)
                    
                })
                .catch(error => {
                    console.log('error occured')
                })
        },

        async changePassword(){
            
            if(this.new_pass == this.confirm_pass && this.new_pass.length >= 8 && this.old_pass.length> 6){
                this.change_pass_error_message = ""

                const data = {
                    token:this.$store.state.token,
                    username:this.$store.state.username,
                    old_password:this.old_pass,
                    new_password:this.new_pass,
                }

                console.table(data)

                await axios
                    .post('/api/v1/user/change/change-password/', data)
                    .then(response => {
                        console.log(response.data)
                        if(response.data.message == "success"){
                            toast({
                                message: `Password Changed Successfully`,
                                type: 'is-success',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 3000,
                                position:'bottom-right',
                            })
                            
                            axios.defaults.headers.common["authorization"] = ""

                            localStorage.removeItem("token")
                            localStorage.removeItem("username")
                            localStorage.removeItem("userid")
                            localStorage.removeItem('cT')
                            this.$store.commit('removeToken')

                            this.$router.push('/login')
                        } else {
                            if(response.data.message == "incorrect_old_password"){
                                this.change_pass_error_message = "Old Password Incorrect"
                                
                                toast({
                                    message: `Incorrect Old Password`,
                                    type: 'is-danger',
                                    dismissible: true,
                                    pauseOnHover: 'true',
                                    duration: 3000,
                                    position:'bottom-right',
                                })
                                
                            }
                        }
                    })
                    .catch(error => {
                        if (error.response) {
                            for (const property in error.response.data) {
                                this.errors.push(`${property}: ${error.response.data[property]}`)
                            }
                        } else {
                            this.errors.push("Something went wrong")
                            console.log(JSON.stringify(error))
                        
                        }
                        toast({
                            message: `An error occured, Please check your network and try again`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
            })


            }else{
                if(this.new_pass != this.confirm_pass){
                    this.change_pass_error_message = " New Password and Confirm Password Dont Match"
                }else if (this.old_pass.length == 0 || this.new_pass.length == 0 || this.confirm_pass.length == 0) {
                    this.change_pass_error_message = "Complete All inputs"
                }else if (this.new_pass.length < 8){
                    this.change_pass_error_message = "New Password Not Long Enough"
                }else{
                    this.change_pass_error_message = "Check all inputs and try again"
                }
            }
            
        }
    }
}

</script>

<style>
    .profile-main{
        background-color: var(--light);
        padding: 1rem;
        border-radius: 1rem;
    }
    .profile-main tr{
        border-bottom:1px solid var(--grey);
        color:var(--dark)
    }

    
    #pass_btn{
        width:100% !important;
        height:2rem;
        border-radius:.5rem;
        background-color:var(--blue);
        border:2rem;
        color:var(--light)
    }
   
    .field p, .field input{
        color:var(--dark)
    }

    td input, td select{
        width:100%;
        height:2rem;
        padding:0 .5rem;
        margin-left:.5rem;
        
    }
    td textarea{
        resize:none;
        padding:0 .5rem;
        font-size: 1rem;
        margin-left:.5rem;
    }

    main #saveprofile {
        margin-top:1rem;
        background-color:var(--green);
        color:var(--light);
        padding:0.2rem .5rem;
        width:9.6rem;
    }

    main .password-button .pass-button{
        background-color:var(--blue);
        width:11rem;
        text-align: center;
        padding:.5rem;
        color:var(--light);
        border-radius: .5rem;
        margin-top:4rem;
    }

    @media screen and (max-width:400px) {
        td input, td select, textarea{
            position: relative;
            width:100% !important;
        }
    }

</style>