<template>
  <main>
    <div style="justify-content:flex-end;width:100%;text-align:right;cursor:pointer;padding:0 0rem;" @click="isCreate = true"><span style="color:var(--light);background-color:var(--blue);padding:.5rem .5rem; border-radius:1rem;">Create User </span></div>
    <div class="modal" :class="{'showform':isCreate}">
        <div class="modal-content" style="max-height:35rem;">
            <div class="xbar">
                <a href="#"  @click="isCreate = false"><i class='material-icons'>close</i></a> 
             </div>

             <div class="switchbar" style="text-align:center;">
                <span style='font-size:12px;padding:.5rem;border:1px solid var(--blue);border-radius:.5rem;border-top-right-radius: 0;border-bottom-right-radius: 0;background-color:var(--blue);color:var(--light);' v-if="switchcase==1">Single User Create</span>
                <span style='font-size:12px;padding:.5rem;border:1px solid var(--dark);border-radius:.5rem;border-top-right-radius: 0;border-bottom-right-radius: 0;border-right:none;' v-if="switchcase==2" @click="switchcase=1">Single User Create</span>
                <span style='font-size:12px;padding:.5rem;border:1px solid var(--blue);border-radius:.5rem;border-top-left-radius: 0;border-bottom-left-radius: 0;background-color:var(--blue);color:var(--light);' v-if="switchcase==2" >Bulk Registration</span>
                <span style='font-size:12px;padding:.5rem;border:1px solid var(--dark);border-radius:.5rem;border-top-left-radius: 0;border-bottom-left-radius: 0;border-left:none;' v-if="switchcase==1" @click="switchcase=2">Bulk Registration</span>
             </div>

             

             <div class="field" v-if="switchcase == 1" style="margin-top:2rem;"><h3 style="font-size:1.5rem !important;font-weight:600;color:var(--dark);">Register {{userType}}</h3></div>
             <div class="field" v-if="switchcase == 2" style="margin-top:2rem;"><h3 style="font-size:1.5rem !important;font-weight:600;color:var(--dark);">Student Bulk Registration</h3></div>
             <div class="field" v-if="switchcase == 2">
               <span style="color:var(--red);text-decoration:none !important;font-weight:600;">IMPORTANT !!! FORMAT MUST BE FOLLOWED AS IT IS SHOWN BELOW </span>
               <span style="color:var(--red);text-decoration:none !important;font-weight:600;">IMPORTANT !!! default password for bulk registration is set to calebuniv </span>
                <h3 style="text-decoration: underline;">CSV Columns Format: </h3>
                <div style="margin-bottom:2rem;">
                    <h3>Email Address</h3>
                    <h3>Matric No</h3>
                    <h3>Gender</h3>
                    <h3>FullName</h3>
                    <h3>Hostel</h3>
                    <h3>Level</h3>
                    <h3>College</h3>
                    <h3>Department</h3>
                    <h3>Home Address</h3>
                    <h3>Student Phone Number</h3>
                    <h3>Other Student Phone Number</h3>
                    <h3>Parent Email</h3>
                    <h3>Parent Phone Number</h3>
                    <h3>Other Parent Phone Number</h3>
                    <h3>Parent Name</h3>
                </div>

                <label for="" >Select CSV File:</label>
                <progress v-if="document" :value="progress" max="100"></progress>
                <input @change="selectFile" type="file" ref="file" accept="text/csv" style="border:none !important;margin-top:.5rem">
                
                <button v-if="document" class="" @click="uploadFile">Upload</button>
             </div>

             <div class="field" v-if="switchcase == 1">
                <label for="">User Type </label>
                <select v-model="userType">
                    <option value="student">Student</option>
                    <option value="parent">Parent</option>
                    <option value="porter">Porter</option>
                    <option value="saffairs">Student Affairs</option>
                    <option value="security">Security</option>
                </select>
             </div>

             <div class="field" v-if="switchcase==1" id='formRegister'>
               <label for="" v-if="userType=='student'">Matric No.</label>
                <input v-if="userType=='student'" required type="text" v-model="signupData.matric_no">
              
                <label for="">Email Address</label>
                <input required type="text" v-model="signupData.email">
                  <label for="">Fullname (Surname Firstname Othername)</label>
                <input required type="text" v-model="signupData.fullname">

                <label for="">Gender</label>
                <select v-model="signupData.gender">
                    <option value="">Select Gender</option>
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                   
                </select>
                
                <label for="" v-if="userType=='student' || userType=='porter'">Hostel</label>
                <select v-if="userType=='student' || userType=='porter'" v-model="signupData.hostel">
                    <option value="" selected disabled>Select Hostel</option>
                        <option v-for="hostel in hostels" :key="hostel" :value="hostel.hostel_name">{{hostel.hostel_name}}</option>
                    
                </select>
              
                <label for="" v-if="userType=='student'">Level</label>
                <select v-if="userType=='student'" v-model="signupData.level">
                    <option value="" seslected disabled>Select Level</option>
                    <option value="100">100 Level</option>
                    <option value="200">200 Level</option>
                    <option value="300">300 Level</option>
                    <option value="400">400 Level</option>
                </select>

                <label for="" v-if="userType=='student'">College</label>
                <select name="" v-if="userType=='student'" v-model="signupData.college">
                    <option value="" selected disabled>Select College</option>
                    <option v-for="college in colleges" :key="college" :value="college.colleges">{{ college.colleges.split('').join('.') }}</option>
                   
                </select>

                <label v-if="userType=='student'">Department</label>
                <select name="" v-if="userType=='student'" v-model="signupData.department">
                    <option value="" selected disabled>Select Department</option>
                    <option v-for="department in departments" :key="department" :value="department.department"  >{{department.department}}</option>
                
                </select>


                <label for="">Phone Number</label>
                <input required type="text" v-model="signupData.phone_number">
                <label for="">Other Phone Number</label>
                <input  type="text" v-model="signupData.alt_phone_number">
                
                <label for="" v-if="userType=='student'" >Parent/Guardian Email Address</label>
                <input required type="text" v-if="userType=='student'" v-model="signupData.parent_email">
                
                <label for="" v-if="userType=='student'" >Parent/Guardian Fullname</label>
                <input required type="text" v-if="userType=='student'" v-model="signupData.parent_name">
                
                <label for="" v-if="userType=='student'" >Parent/Guardian Phone No.</label>
                <input required type="text" v-if="userType=='student'" v-model="signupData.parent_phone_number">
                
                <label for="" v-if="userType=='student'" >Alternate Parent/Guardian Phone No.</label>
                <input required type="text" v-if="userType=='student'" v-model="signupData.alt_parent_phone_number">
                
                
                <label for=""  v-if="userType=='student' || userType=='parent'">Home Address</label>
                <textarea v-model="signupData.address"  v-if="userType=='student' || userType=='parent'" required name="" id="" cols="30" rows="3" style="resize:none;margin-bottom:2rem !important;"></textarea>
                
                <div class="passwordbar" style="width:100%">
                    <input type="password" style="width:70%" id='passwordtext' placeholder="set user password" v-model="signupData.password">
                    <span class="material-icons" style="width:30%;margin-top:.5rem;" id='eye' @click="showPass=!showPass;switchtype()" v-if="showPass==false">visibility</span>
                    <span class="material-icons" style="width:30%;margin-top:.5rem;" id='eye' @click="showPass=!showPass;switchtype()" v-if="showPass==true">visibility_off</span>
                </div>
               
                <button @click="RegisterUser();" style="border:none;background-color:var(--blue);color:var(--light);padding:.5rem;font-size: 1rem; border-radius:.5rem;">Submit</button>
            </div>
        </div>
    </div>
    
    <div class="modal" :class="{ 'showform': studentDetailPanel }">
        <div class="modal-content" style="border-radius:1rem;">
             <div href="#" class="closebutton" @click="studentDetailPanel = !studentDetailPanel" style="text-align:right !important; width:100%;">
                    <a href="#"><i class='material-icons' style="color:var(--red);font-size:36px;">close</i></a>
                </div>
            <div class="studentinfo">
               
                <div class="imag">
                    <img :src="(basemediaURL+this.studentDetailList.image || '../assets/profile.png')" style="margin-top:.5rem;width:128px;height:128px;border-radius:50%;">
                </div>
                <div class="studentdetails">
                    <table>
                        <tr class="detailtext">
                            <td>Surname</td>
                            <td>: {{ this.studentDetailList.lastname || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Firstname</td>
                            <td>: {{ this.studentDetailList.firstname || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Middlename</td>
                            <td>: {{ this.studentDetailList.middlename || 'Not Set'}}</td>
                        </tr>

                        <tr class="detailtext" v-if="studentDetailList.username">
                            <td>Matric Number</td>
                            <td>: {{studentDetailList.username.replace('-','/') || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>level</td>
                            <td>: {{this.studentDetailList.level|| 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>college</td>
                            <td>: {{this.studentDetailList.college|| 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>department</td>
                            <td>: {{this.studentDetailList.department|| 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Gender</td>
                            <td>: {{ this.studentDetailList.gender || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Hostel</td>
                            <td>: {{ this.studentDetailList.hostel || 'Not Set'}} Hall</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Email</td>
                            <td>: {{ this.studentDetailList.email || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Phone Number</td>
                            <td>: {{ this.studentDetailList.phonenumber || 'Not Set'}},{{ studentDetailList.alternate_number }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Parent Phone Number</td>
                            <td>: {{ this.studentDetailList.parent_no || 'Not Set'}}, {{this.studentDetailList.alternate_parent_no}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Parent Email</td>
                            <td>: {{ this.studentDetailList.parent_email || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Home Address</td>
                            <td>: {{ this.studentDetailList.home_address || 'Not Set'}}</td>
                        </tr>
                        <tr class="detailtext" v-if="studentDetailList.date_added"> 
                            <td>Date Added</td>
                            <td>: {{studentDetailList.date_added.split('T')[0] || 'Not Set'}} on {{studentDetailList.date_added.split('T')[1].split('.')[0] || 'Not Set'}}</td>
                        </tr>
                    </table>

                </div>
            </div>
        </div>
    </div>
    
    
    <div class="table-data">
        <div class="users">
            <span @click="user_to_manage = 'students'; this.switchUser()" v-if="user_to_manage != 'students'">Manage Students</span>
            <span @click="user_to_manage = 'parents'; this.switchUser()" v-if="user_to_manage != 'parents'">Manage Parents</span>
            <span @click="user_to_manage = 'staff'; this.switchUser()" v-if="user_to_manage != 'staff'">Manage Staff</span>
        </div>

        <div class="users" style="justify-content:left;display:flex;flex-direction:column;" >
           
            <h3 v-if="user_to_manage" style="text-transform: capitalize;margin-left:1rem;font-size: 1.5rem;font-weight:600;">Manage {{user_to_manage}}</h3>
             
            <input type="text" v-model="searchQuery" placeholder="Search in Table" id='tablesearch' style="margin-top:1rem; max-width:20rem">

        </div>


        
        

        <div class="order" style="width:100%;overflow-y:scroll;max-height:40rem;">
            
            
            <table id='table' style="margin-top:2rem;">
                <thead>
                    <tr>
                        <th class='keep-on-top'>Username</th>
                        
                        <th>Fullname</th>
                        <th>User Type</th>
                        <th>gender</th>
                        <th>Date Created</th>
                        <th v-if="user_to_manage =='students'">Email</th>
                        <th>Phone Number</th>
                        <th v-if="user_to_manage=='students' || user_to_manage=='staff'">Hostel</th>
                        
                    
                    </tr>
                </thead>
                <tbody v-if="user_to_manage=='students'">
                    <tr v-for="user in filteredUsers" :key="user">
                        <td style='color:#3f0a70 !important;cursor:pointer;'><span @click="showDetails(user.username)">{{ user.username.replace('-','/') }}</span></td>
                        <td style="text-transform: capitalize;">{{user.lastname}} {{user.firstname}} {{user.middlename}}</td>
                        <td>{{user.usercat}}</td>
                        <td>{{user.gender}}</td>
                        <td>{{user.date_added.split('T')[0]}} on {{ user.date_added.split('T')[1].split('.')[0] }}</td>
                        <td>{{user.email}}</td>
                        <td>{{ user.phonenumber }}</td>
                        <td>{{user.hostel}}</td>
                      
                    </tr>
                    
                </tbody>
                <tbody v-else-if="user_to_manage=='parents'">
                    <tr v-for="user in filteredUsers" :key="user">
                        <td>{{ user.username }}</td>
                        <td style="text-transform: capitalize;">{{user.lastname}} {{user.firstname}} {{user.middlename}}</td>
                        <td>{{user.usercat}}</td>
                        <td>{{user.gender}}</td>
                        <td>{{user.date_added.split('T')[0]}} on {{ user.date_added.split('T')[1].split('.')[0] }}</td>
                      
                        <td>{{ user.phonenumber }}</td>
                       
                    </tr>
                    
                </tbody>
                <tbody v-else-if="user_to_manage=='staff'">
                    <tr v-for="user in filteredUsers" :key="user">
                        <td>{{ user.username }}</td>
                        <td style="text-transform: capitalize;">{{user.lastname}} {{user.firstname}} {{user.middlename}}</td>
                        <td>{{user.usercat}}</td>
                        <td>{{user.gender}}</td>
                        <td>{{user.date_added.split('T')[0]}} on {{ user.date_added.split('T')[1].split('.')[0] }}</td>
                        
                        <td>{{ user.phonenumber }}</td>
                        <td>{{user.hostel}}</td>
                       
                    </tr>
                    
                </tbody>
            </table>
        </div>




        <div class="table-data" >

        <div class="todo" style="overflow-x:hidden !important;">
            <div style="margin-top:2rem;width:100%;border-top-left-radius: 0;border-top-right-radius: 0;">
                               
            </div>



            <div class="head">
                
                <h3>Registration Logs</h3>
                
            </div>

            <div style="max-height:40rem; overflow-y:scroll;">
                <ul class="todo-list" v-for="event in logs" :key="event" >
                
                    <li class="primary" style="margin-bottom: 1rem;">
                        <div> 
                            <p>{{ event }}</p>
                        </div>
                    </li>
                    
                </ul>
            </div>
           
        </div>


        
    </div>
    
        
        
    </div>

    
  </main>
</template>

<script>



import axios from 'axios';


import { toast } from 'bulma-toast';


export default {
    name:'Users',
    data(){
        return {
            users_list:this.$store.state.UAD,
            users: [],
            user_to_manage:'students',
            basemediaURL:axios.defaults.baseURL+'/media/',
            customSearchQuery:'Username',
            searchQuery:'',
            isCreate:false,
            showPass:false,
            switchcase: 1,
            colleges: '',
            studentDetailList:'',
            studentDetailPanel:false,
            departments:'',
            hostels:'',
            userType:'',
            mat_no_list:'',
            progress:0,
            document:null,
            logs:[],
            signupData:{
                usercat:this.userType,
                hostel:'',
                email:'',
                matric_no:'',
                phone_number:'',
                alt_phone_number:'',
                parent_email:'',
                parent_name:'',
                parent_phone_number:'',
                alt_parent_phone_number:'',
                college:'',
                department:'',
                address:'',
                gender:'',
                fullname:'',
                level:'',
                mat_no_list:[],
                password:'',
            },
            logTimer:true,
        }
    },
    computed: {
        filteredUsers: function(){
                this.switchUser();
                console.log(this.users)
                const searchQuery = this.searchQuery.trim().toLowerCase().replace('/','-')
                return this.users.filter((user) => {
                   
                    return user.username.toLowerCase().match(searchQuery) || 
                    user.email.toLowerCase().match(searchQuery) ||
                    user.phonenumber.toLowerCase().match(searchQuery) ||
                    user.lastname.toLowerCase().match(searchQuery) ||
                    user.firstname.toLowerCase().match(searchQuery) 
                    
                    
            })
            
        }
    },
    mounted(){
        this.switchUser();
        this.getUsers();
        document.title = 'Caleb Exeats | Admin'
        localStorage.setItem('cT','2')
        this.timer4 = setInterval(this.checkAuthStat, 1000) 
        this.timer5 = setInterval(this.getLogs, 5000) 
        if( this.$store.state.usercat!=='superuser'){
            this.$router.push('/');
        }
        this.getFormData();
    },

    methods:{
        switchtype(){
            const input = document.getElementById('passwordtext');
            if(input.type=='text'){
                input.type='password'
            } else {
                input.type='text'
            }
        }
        ,
        async getLogs(){
            await axios 
                .get(`api/v1/account/register/logs/${this.$store.state.token}/`)
                .then(response => {
                    this.logs = response.data.logs
                    if(this.logs.length<1){
                        clearInterval(this.timer5)
                    }
                })
        },
        switchUser(){
            if(this.user_to_manage=='students'){
                this.users=this.users_list.students
            } else if(this.user_to_manage=='parents'){
                this.users=this.users_list.parents
            } else if(this.user_to_manage=='staff'){
                this.users=this.users_list.staff
            } 

            console.log(this.users)
        },
        
        checkAuthStat(){
            if ( !this.$store.state.isAuthenticated || this.$store.state.token == '' ){
                this.logout();
            } 
  
        },
        async showDetails(info) {
            const mat_no = info
            this.studentDetailList=''
            this.studentDetailPanel = true;
            if(this.user_to_manage=='students'){
                await axios
                .get(`api/v1/students-data/${this.$store.state.username}=|=${mat_no}`)
                .then(response => {
                    this.studentDetailList = response.data
                    console.table(this.studentDetailList)
                })
                .catch(error => {
                    console.log("an error occured ")
                })
            } else if (this.user_to_manage=='staff' || this.user_to_manage=='parents') {
                await axios
                .get(`api/v1/other-data/${info}`)
                .then(response => {
                    this.studentDetailList = response.data
                    console.table(this.studentDetailList)
                })
                .catch(error => {
                    console.log("an error occured ")
                })
            }

        },
        async getUsers(){
            await axios
                .get(`/api/v1/account/administrator/user-data/${this.$store.state.token}/`)
                .then(response =>{
                    this.$store.commit('setAdminData',response.data)
                    this.users_list = response.data
                    this.$store.commit('setUserDataAdmin',response.data)
                    
                })
        },

        async getFormData() { 
            if (this.$store.state.isAuthenticated) {
                await axios 
                    .get('api/v1/form/hostels')
                    .then(response => {
                        this.hostels = response.data
                    })
                    .catch(error => {
                        toast({
                            message: `could not get data from server. check connection and try again`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-right',
                        })
                    })

                await axios 
                    .get('api/v1/form/colleges')
                    .then(response => {
                        this.colleges = response.data
                    })
                    .catch(error => {
                        toast({
                            message: `could not get data from server. check connection and try again`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-right',
                        })
                    })

                await axios 
                    .get('api/v1/form/departments')
                    .then(response => {
                        this.departments = response.data
                    })
                    .catch(error => {
                        toast({
                            message: `could not get data from server. check connection and try again`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-right',
                        })
                    })

            }
        },
        
        
        async logout(){

            const token = this.$store.state.token
            axios.defaults.headers.common["authorization"] = ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")
            localStorage.removeItem('cT')
            this.$store.commit('removeToken')
            await axios
                .post('api/v1/person/logout/',{token:token},)
                .then(response => {
                    toast({
                            message: `${response.data.message}`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-right',
                        })
                    
                })


            this.$router.push('/login')

},
        selectFile() {
			this.document = this.$refs.file.files.item(0)
			if(this.document.size > (1048576 * 5)){
				this.document = null;
				toast({
						message: `Image file too Large. Image must not be more than ${this.imageUploadSize}MB`,
						type: 'is-danger',
						dismissible: true,
						pauseOnHover: 'true',
						duration: 5000,
						position:'top-center',
					})
			}
		},

		uploadFile(){
			this.progress = 0;

			this.performUpload(this.document, event => {
				this.progress = Math.round((100 * event.loaded) / event.total)
			})
			.then(response => {
				this.message="Image uploaded successfully";
				this.document=null
                
			})
			.catch(error => {
				this.progress=0;
				this.message = "something went wrong, please try again!";
				this.document=null
			})

		},
		async performUpload(file, onuploadProgress) {
			let formData = new FormData();
			formData.append('csvfile', file)
			
			//formData.append('token', this.$atore.state.token)
			console.log(formData.get('csvfile'))
			console.log('ejk')
			console.log(file)

			return axios
				.post('/api/v1/account/register/bulk/', {token:this.$store.state.token,password:'admin',formdata:formData.get('csvfile')}, {
					headers: { 
						"Content-Type":"multipart/form-data",

					},
					onuploadProgress

				})
				.then(response => {
					console.log(response.data)
					this.progress = 100;

					
					toast({
                            message: `File Uploaded Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-center',
                        })
						
					this.document = null
					
				})

		},
		
    async RegisterUser(){
        var validated = false;
        var data = {}
        if(this.userType.toLowerCase() == 'student'){
            if(
                this.signupData.matric_no.trim().length < 7 ||
                this.signupData.hostel.trim() == '' ||
                this.signupData.fullname.trim() == '' ||
                this.signupData.college.trim() =='' ||
                this.signupData.email.trim() == '' ||
                this.signupData.gender.trim() == '' ||
                this.signupData.parent_email == '' ||
                this.signupData.student_level == '' ||
                this.signupData.phone_number == '' ||
