<template>
<main id='main'>
    <!---------CREATE REQUEST-->
    <div v-if="$store.state.usercat == 'student'" class="modal" :class="{'showform': showForm}">
        <form class="modal-content" style='max-width:150% !important; padding:1rem !important;'>
            <div class="xbar">
                <a href="#" @click="showForm = false"><i class='material-icons'>close</i></a>
            </div>
            <div class="exeattype">
                <p class="category">Exeat Type:</p>

                <select name="exeat_type" v-model="exeat_type" id="exeat-type" required>
                    <option value="" selected disabled>Select Exeat Type</option>
                    <option value="short">Short Exeat</option>
                    <option value="long">Long Exeat</option>
                </select>

            </div>
            <div class="dates">

                <div class="departure">
                    <p class="category">
                        <span v-if="exeat_type=='Short Exeat'">Departure and Arrival date</span>
                        <span v-else>Departure date:</span>
                    </p>
                    <input type="date" v-model="departure_date" id="departure_date_picker" :max="arrival_date" required>
                </div>

                <div class="arrival" id='arrival-date' v-if="exeat_type != 'Short Exeat'">
                    <p class="category">Arrival date:</p>
                    <input type="date" v-model="arrival_date" id="arrival_date_picker" :min="departure_date" required>

                </div>

                <p style="color:var(--dark)">Description: </p>
                <select v-model="exeat_description_default" name="" id="disableselectdesc" v-if="showcustom == false && exeat_type!='Short Exeat'">
                    <option value="" selected disabled>choose description..</option>
                    <option v-for="reasons in descriptions" :key="reasons" :value="reasons.description">{{ reasons.description }}</option>
                </select>

                <div class="customdescbar" style="display:flex">
                    <p>Custom:</p> <span><input type="checkbox" name="" id="" style="margin-top:-.8rem;margin-left:.5rem;width:1rem;height:3rem;" @click="showcustom = !showcustom"></span>
                </div>

                <div id="customdesc" v-if="showcustom == true || exeat_type=='Short Exeat'">
                    <p class="category" style='margin-top:-1rem;' id="messagebelow">before submitting a request, Please give a detailed description of your request: </p>
                    <textarea v-model="exeat_description_custom" name="main_box" id="" cols="30" rows="10" minlength=10 maxlength=250 placeholder="Request Description" required></textarea>

                </div>
                <div class="button-submit" style="background-color:var(--blue);padding:.5rem;border-radius:.5rem;text-align: center;">
                    <span id="buttonsub" style="color:var(--light);padding:.5rem auto !important;cursor:pointer;" @click="sendRequest()">Submit Request</span>
                </div>

            </div>
        </form>
    </div>

    <!---------------student info check ------>
    <div v-if="$store.state.usercat != 'student'" class="modal2" :class="{ 'showInfo': studentDetailPanel }">
        <div class="modal-content2" style="border-radius:1rem;">
            <div class="studentinfo">
                <div href="#" class="closebutton" @click="studentDetailPanel = !studentDetailPanel">
                    <a href="#"><i class='material-icons' style="color:var(--red);font-size:36px;">close</i></a>
                </div>
                <div class="imag">
                    <img :src="base_URL +'/media/'+this.studentDetailList.image" alt="" style="margin-top:.5rem;width:128px;height:128px;border-radius:50%;">
                </div>
                <div class="studentdetails">
                    <table>
                        <tr class="detailtext">
                            <td>Surname</td>
                            <td>: {{ this.studentDetailList.lastname }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Firstname</td>
                            <td>: {{ this.studentDetailList.firstname }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Middlename</td>
                            <td>: {{ this.studentDetailList.middlename }}</td>
                        </tr>

                        <tr class="detailtext">
                            <td>Matric Number</td>
                            <td>: {{this.studentDetailList.username}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>level</td>
                            <td>: {{this.studentDetailList.level}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>college</td>
                            <td>: {{this.studentDetailList.college}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Gender</td>
                            <td>: {{ this.studentDetailList.gender }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Hostel</td>
                            <td>: {{ this.studentDetailList.hostel }} Hall</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Email</td>
                            <td>: {{ this.studentDetailList.email }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Phone Number</td>
                            <td>: {{ this.studentDetailList.phonenumber }}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Parent Phone Number</td>
                            <td>: {{ this.studentDetailList.parent_no }}, {{this.studentDetailList.alternate_parent_no}}</td>
                        </tr>
                        <tr class="detailtext">
                            <td>Home Address</td>
                            <td>: {{ this.studentDetailList.home_address }}</td>
                        </tr>
                    </table>

                </div>
            </div>
        </div>
    </div>


    <div v-if="$store.state.usercat === 'student' " style="overflow-y:hidden !important">
            <div class="head-title">
                <div class="left">
                    <h1 v-if="$store.state.usercat !='security'" style="text-transform:capitalize;font-size:1.5rem !important;">{{ greeting() }}, <span v-if="$store.state.usercat != 'student' "><span v-if="$store.state.userdata.gender=='female'">Mrs.</span> <span v-else-if="$store.state.userdata.gender=='male'">Mr.</span> </span> <span v-if="$store.state.usercat=='student'">{{ $store.state.userdata.firstname }}</span> <span v-else-if="$store.state.usercat!='student'">{{ $store.state.userdata.lastname }}</span> </h1>
        
                    <h3 style="text-transform:capitalize;" v-if="$store.state.usercat=='porter'">{{ $store.state.userdata.hostel }} Hall Exeat Requests</h3>
                </div>
                <a v-if="$store.state.usercat == 'student'" href="#" class="btn-download" @click="showForm = !showForm">
                    <span class="text">Create Request</span>
                    <i class='material-icons' style="margin-left:-1rem;">add</i>
                </a>
            </div>
        
            <ul class="box-info" v-if="$store.state.usercat != 'security'">
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFF2C6;color:#FFCE26;padding:.8rem;border-radius:1rem">pending_actions</i>
                    <span class="text">
                        <p>Pending Requests</p>
                        <h3>{{ $store.state.userdata.pendingexeats }}</h3>
        
                    </span>
                </li>
               
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#D1FFD0;color:#2CFF26;padding:.8rem;border-radius:1rem">check_box
        
                    </i>
                    <span class="text">
                        <p>Approved Requests</p>
                        <h3>{{ $store.state.userdata.approvedexeats }}</h3>
        
                    </span>
                </li>
                <li>
                    <i class='material-icons' style="font-size:48px;background-color:#FFE0D3;color:#FD7238;padding:.8rem;border-radius:1rem">dangerous</i>
                    <span class="text">
                        <p>Rejected Requests</p>
                        <h3>{{ $store.state.userdata.rejectedexeats }}</h3>
        
                    </span>
                </li>
            </ul>
    </div>

    <DashboardHeader v-else />


    <!----------------------------------STUDENT------------------>
    <div v-if="usercat === 'student'" class="table-data">
        <div class="order" style="overflow-y: scroll; max-height:30rem;">
            <div class="head" v-if="this.recentForms.length >= 1" style='padding-left:1rem;'>
                <span>
                    <h3>Recent Requests</h3>
                    <router-link to="/history" id="view_all" style="margin-top:0px;"><span>view all</span></router-link>

                </span>

            </div>
            <div class="head" v-else-if="this.recentForms.length < 1">
                <h3 style="font-size:16px;">No Recent Requests Yet!. <p style="color:#aaa">click the button on the top of the page to create one!</p>
                </h3>
            </div>
            <table>
                <thead>
                    <tr>
                        <th class='keep-on-top' style='padding-left:30px !important;'>Form ID</th>
                        <th>Exeat Type</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Description</th>
                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Departure Date</th>
                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Arrival Date</th>
                        <th>porter's Approval</th>
                        <th>parent's Approval</th>
                        <th>S.A Approval</th>
                        <th>Final Remarks</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">cancel</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="form in recentForms" :key="form.id">
                        <td>CULEXT{{ form.id }}</td>
                        <td>{{ form.exeat_type }}</td>
                        <td>{{ form.date_added_main }}</td>

                        <td>{{ form.exeat_description }}</td>
                        <td>{{ form.departure_date }}</td>
                        <td>{{ form.arrival_date }}</td>

                        <td v-if="form.statusPorter == '0'"><span class="status process">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>

                        <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>

                        <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>

                        <td v-if="form.iscancelled"><span class="status rejected">Cancelled</span></td>
                        <td v-else-if="form.statusPorter == '2' || form.statusParent == '2' || form.statusStudentAffairs == '2' "><span class="status rejected">Rejected</span></td>
                        <td v-else-if="form.statusPorter == '1' && form.statusParent == '1' &&  form.statusStudentAffairs == '1' "><span class="status completed">Approved</span></td>
                        <td v-else-if="form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0' "><span class="status process">Pending</span></td>
                        <td style="text-align: left;"><a v-if="(form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0') && !form.iscancelled" id="cancelstudentbutton"  @click="cancelForm(form.id)">Cancel</a></td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div class="todo">
            <div class="head">
                <span style="display:flex;flex-direction: column;">
                    <h3>Recent Events</h3>
                    <router-link to="/events" id="view_all" style="margin-top:0px;"><span>view all</span></router-link>
                </span>

            </div>
            <div style="max-height:25rem; overflow-y:scroll;">
                <ul class="todo-list" v-for="event in events" :key="event">
                    <li v-if="event.tag == 'success'" class="completed" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else-if="event.tag == 'danger'" class="danger" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else class="primary" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>

                </ul>
            </div>

        </div>
    </div>

    <!----------------------------------- PORTER --------------------------->
    <div v-if="usercat === 'porter'" class="table-data">
        
            <div class="todo" id="whenBig">
                <div class="head" style="max-height:4rem">
                    <span style="display:flex;flex-direction: column;">
                        <h3 >Search All Requests</h3><span><input type="text" style="height:35px;padding-left:10px;font-size:1rem;margin:1rem 0rem;border-radius:.5rem;border:1px solid var(--dark)" placeholder="Request ID/Matric No." maxlength="20" v-model="searchedtext"> <span style="background-color:var(--blue);color:var(--light);padding:.4rem .5rem;border-radius:.5rem" @click="getSearchedForms()">Search</span></span>
                    </span>

                </div>

                <div class="modal" :class="{'showform':isSearch && searchedForms.length > 0}">

                    <div class="modal-content">
                        <div class="xbar">
                            <a href="#" @click="SearchedFormsClear();"><i class='material-icons'>close</i></a>
                        </div>
                        <div class="order" style="overflow-y: scroll;max-height: 40rem; max-width: 60rem;border-radius:.5rem">

                            <table v-if="searchedForms.length > 0">
                                <thead>
                                    <tr>

                                        <th class="keep-on-top" style="padding-left:2rem !important; z-index: 9999 !important;">Form ID</th>
                                        <th style="width:1500px !important;">Fullname</th>
                                        <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Matric No.</th>

                                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>

                                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Duration</th>

                                        <th>parent's Approval</th>
                                        <th>S.A Approval</th>
                                        <th>porter's Approval</th>
                                        <th style="padding:0rem 12rem 0rem 1rem;text-align:left;">Final Remarks</th>

                                    </tr>
                                </thead>
                                <tbody v-for="form in searchedForms" :key="form.id">
                                    <tr :id="`pform${form.id}`">

                                        <td>CULEXT{{ form.id }}</td>

                                        <td><a href="#" style="text-align: center;" @click="showDetails(form.matric_no)"><img class="request_img" :src="form.studentpic" alt="">{{ form.fullname }}</a></td>
                                        <td>{{ form.matric_no }}</td>
                                        <td>{{ form.date_added_main }}</td>

                                        <td>{{ form.departure_date }} to {{ form.arrival_date }}</td>

                                        <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                                        <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                                        <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>

                                        <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                                        <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                                        <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>

                                        <td v-if="form.statusPorter == '0'">
                                            <div class="choice" v-if="!form.iscancelled"><button class="approve" @click="this.approveRequest(form.id, form.matric_no)">Approve Request</button><button class="reject" @click="rejectRequest(form.id, form.matric_no)">Reject Request</button></div><span class="status process" v-else>{{ checkStatus(form.statusPorter) }}</span>
                                        </td>
                                        <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                                        <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>

                                        <td v-if="form.iscancelled"><span class="status rejected">Cancelled by student</span></td>
                                        <td v-else-if="form.statusPorter == '2' || form.statusParent == '2' || form.statusStudentAffairs == '2' "><span class="status rejected">Rejected</span></td>
                                        <td v-else-if="form.statusPorter == '1' && form.statusParent == '1' &&  form.statusStudentAffairs == '1' "><span class="status completed">Approved</span></td>
                                        <td v-else-if="form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0' "><span class="status process">Pending</span></td>

                                    </tr>
                                </tbody>
                            </table>

                        </div>

                    </div>
                </div>

            </div>
            <div class="order" style="overflow-y: scroll; max-height:30rem;">

                <div class="head" style="display:flex;flex-direction:column;" v-if="this.recentForms.length >= 1">
                     <h3 style="text-transform: capitalize;margin-left:2rem !important;">Pending Requests</h3>

                </div>

                <div class="head" v-else-if="this.recentForms.length < 1 ">
                    <h3 style="margin-left:2rem !important">No Pending Requests Yet!</h3>
                </div>
                <table>
                    <thead>
                        <tr>

                            <th class="keep-on-top" style="padding-left:2rem !important; ">Form ID</th>
                            <th style="width:1500px !important;">Fullname</th>
                            <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Matric No.</th>

                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>
                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Description</th>
                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Departure Date</th>
                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Arrival Date</th>

                            <th>parent's Approval</th>
                            <th>S.A Approval</th>
                            <th>porter's Approval</th>

                        </tr>
                    </thead>
                    <tbody v-for="form in recentFormsFiltered" :key="form.id">
                        <tr v-if="form.statusPorter == 0" :id="`pform${form.id}`">

                            <td @click="viewform = {
                                id:form.id,
                                matric_no:form.matric_no,
                                date_added:form.date_added_main,
                                desc:form.exeat_description,
                                departure_date:form.departure_date,
                                arrival_date:form.arrival_date,
                                statusParent:checkStatus(form.statusParent),
                                statusPorter:checkStatus(form.statusPorter),
                                statusStudentAffairs:checkStatus(form.statusStudentAffairs),
    
                            },showRequestDetails=true" style="cursor:pointer;">CULEXT{{ form.id }}</td>
    
                            <td><a href="#" style="text-align: center;" @click="showDetails(form.matric_no)"><img class="request_img" :src="form.studentpic" alt="">{{ form.fullname }}</a></td>
                            <td>{{ form.matric_no }}</td>
                            <td>{{ form.date_added_main }}</td>

                            <td>{{ form.exeat_description }}</td>
                            <td>{{ form.departure_date }}</td>
                            <td>{{ form.arrival_date }}</td>

                            <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                            <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                            <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>

                            <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                            <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                            <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>

                            <td v-if="form.statusPorter == '0'">
                                <div class="choice"><button class="approve" @click="this.approveRequest(form.id, form.matric_no)">Approve Request</button><button class="reject" @click="rejectRequest(form.id, form.matric_no)">Reject Request</button></div>
                            </td>
                            <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                            <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>

                        </tr>
                    </tbody>
                </table>
            </div>


            <div class="modal" :class="{'showform':showRequestDetails}">
                <div class="modal-content">
                    <div class="xbar">
                        <a href="#" @click="showRequestDetails = false"><i class='material-icons'>close</i></a> 
                    </div>
    
                    <h2 style="font-size:1rem;margin:.5rem 0 0;">Request ID: CULEXT{{viewform.id}}</h2>
                    <h2 v-if="viewform.matric_no">Matric No.: {{ viewform.matric_no.replace('-','/') }}</h2>
                    <h2>Date Sent: {{viewform.date_added}}</h2>
                    <h2>Request Description: {{viewform.desc}}</h2>
                    <h2>Departure Date: {{viewform.departure_date}}</h2>
                    <h2>Arrival Date: {{viewform.arrival_date}}</h2>
                    <h2 style="margin:1rem 0;">Parent's Remark: 
                        <span v-if="viewform.statusParent=='approved'" style="background-color: var(--blue) !important;padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)"> {{viewform.statusParent}}</span>
                        <span v-else-if="viewform.statusParent=='rejected'" class='rejected' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusParent}}</span>
                        <span v-else class='pending' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusParent}}</span>
                    </h2>
                    <h2 style="margin:1rem 0;">Student's Affairs Remark: 
                        <span v-if="viewform.statusStudentAffairs=='approved'" style="background-color: var(--blue) !important;padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)"> {{viewform.statusStudentAffairs}}</span>
                        <span v-else-if="viewform.statusStudentAffairs=='rejected'" class='rejected' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusStudentAffairs}}</span>
                        <span v-else class='pending' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusStudentAffairs}}</span>
                    </h2>
                    <h2 v-if="viewform.statusPorter!='pending'" style="margin:1rem 0;">Student's Affairs Remark: 
                        <span v-if="viewform.statusPorter=='approved'" style="background-color: var(--blue) !important;padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)"> {{viewform.statusPorter}}</span>
                        <span v-else-if="viewform.statusPorter=='rejected'" class='rejected' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusPorter}}</span>
                        <span v-else class='pending' style="padding:.2rem .5rem;border-radius:2rem;margin:1rem 0;color:var(--light)">{{viewform.statusPorter}}</span>
                    </h2>
    
                    <div v-else>
                        <div class="choice"><button class="approve" @click="this.approveRequest(viewform.id, viewform.matric_no);showRequestDetails=false">Approve Request</button><button class="reject" @click="rejectRequest(viewform.id, viewform.matric_no);showRequestDetails=false">Reject Request</button></div>
    
                    </div>
    
                </div>
            </div>

