                <div class="field">
                    <p>Description:</p>
                    <select v-model="extraTask_description" required>
                        <option value="" selected disabled>choose description..</option>
                        <option v-for="reasons in descriptions" :key="reasons" :value="reasons.description">{{ reasons.description }}</option>
                    </select>
                </div>

                <div class="field">
                    <p>Confirm Password:</p>
                    <input type="password" v-model="extraTask_password" required>
                </div>

                <button style="color:var(--light);background-color:var(--blue);padding:.5rem 1rem;border-radius:.2rem;border:none;" @click="customTask()">Submit</button>
            </form>
        </div>

        <div class="todo" style="overflow-x:hidden !important;">
            <div class="head">
                <h3>Recent Events</h3>

            </div>
            <div style="max-height:25rem; overflow-y:scroll;">
                <ul class="todo-list" v-for="event in events" :key="event">
                    <li v-if="event.tag == 'success'" class="completed" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else-if="event.tag == 'danger'" class="danger" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else class="primary" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>

                </ul>
            </div>

        </div>
        <div class="custombuttons" style="display:flex;width:100%;flex-direction: column;">
            <button class="bulk-approval" style="width:10rem;height:2rem;background-color:var(--blue);color:var(--light);border:none;border-radius:.2rem;font-size:1rem;" @click="extraTask = !extraTask">Custom Tasks</button>

        </div>

    </div>

    <!----------------------------------SECURITY----------------------->

    <div v-if="usercat === 'security'" class="table-data">
        <div class="todo">
            <div class="head">
                <span style="display:flex;flex-direction: column;">
                    <h3>Search Requests</h3><span><input type="text" style="height:30px;padding-left:10px;font-size:1rem;margin:1rem 0rem;border-radius:.2rem;border:1px solid var(--dark)" placeholder="Request ID/Matric No." v-model="searchedtext"> <span style="background-color:var(--blue);color:var(--light);padding:.4rem .5rem;" @click="getSearchedForms()">Search</span> <span style="background-color:var(--red);color:var(--light);padding:.4rem .5rem;" @click="SearchedFormsClear();">Close</span></span>
                </span>

            </div>

            <div class="order" style="overflow-y: scroll; max-height:40rem;">

                <table v-if="searchedForms.length > 0">
                    <thead>
                        <tr>

                            <th>Form ID</th>
                            <th style="width:1500px !important;">Fullname</th>
                            <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Matric No.</th>

                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>

                            <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Duration</th>

                            <th>parent's Approval</th>
                            <th>S.A Approval</th>
                            <th>porter's Approval</th>
                            <th style="padding:0rem 12rem 0rem 1rem;text-align:left;">Final Remarks</th>

                        </tr>
                    </thead>
                    <tbody v-for="form in searchedForms" :key="form.id">
                        <tr :id="`pform${form.id}`">

                            <td>CULEXT{{ form.id }}</td>

                            <td><a href="#" style="text-align: center;" @click="showDetails(form.matric_no)"><img class="request_img" :src="form.studentpic" alt="">{{ form.fullname }}</a></td>
                            <td>{{ form.matric_no }}</td>
                            <td>{{ form.date_added_main }}</td>

                            <td>{{ form.departure_date }} to {{ form.arrival_date }}</td>

                            <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                            <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                            <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>

                            <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                            <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                            <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>

                            <td v-if="form.statusPorter == '0'"><span class="status process">{{ checkStatus(form.statusPorter) }}</span></td>
                            <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                            <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>

                            <td v-if="form.iscancelled"><span class="status rejected">Cancelled by student</span></td>
                            <td v-else-if="form.statusPorter == '2' || form.statusParent == '2' || form.statusStudentAffairs == '2' "><span class="status rejected">Rejected</span></td>
                            <td v-else-if="form.statusPorter == '1' && form.statusParent == '1' &&  form.statusStudentAffairs == '1' "><span class="status completed">Approved</span></td>
                            <td v-else-if="form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0' "><span class="status process">Pending</span></td>

                        </tr>
                    </tbody>
                </table>
                <h3 v-else-if="searchedForms.length < 1 && isSearch==true"> No Searches Found</h3>
                <h3 v-else-if="searchedForms.message == 'invalid_search' && isSearch==true"> Invalid Entry</h3>
            </div>

        </div>

        <div class="order" style="overflow-y: scroll; max-height:30rem;">
            <div class="head" v-if="this.recentFormsFiltered.length >= 1">
                <span>
                    <h3>Recent Requests</h3>

                </span>

            </div>
            <div class="head" v-else-if="this.recentFormsFiltered.length < 1">
                <h3 style="font-size:16px;">No Recent Requests Yet!. <p style="color:#aaa">click the button on the top of the page to create one!</p>
                </h3>
            </div>
            <table>
                <thead>
                    <tr>
                        <th>Form ID</th>
                        <th>Exeat Type</th>
                        <th style="padding:0rem 6rem 0rem 1rem;text-align:left;">Date Sent</th>

                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Departure Date</th>
                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Arrival Date</th>
                        <th> Approval</th>
                        <th>parent's Approval</th>
                        <th>S.A Approval</th>
                        <th>Final Remarks</th>

                    </tr>
                </thead>
                <tbody>
                    <tr v-for="form in recentFormsFiltered" :key="form.id">
                        <td>CULEXT{{ form.id }}</td>
                        <td>{{ form.exeat_type }}</td>
                        <td>{{ form.date_added_main.split('on')[0] }}</td>

                        <td>{{ form.departure_date }}</td>
                        <td>{{ form.arrival_date }}</td>

                        <td v-if="form.statusPorter == '0'"><span class="status process">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>

                        <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>

                        <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>

                        <td v-if="form.iscancelled"><span class="status rejected">Cancelled</span></td>
                        <td v-else-if="form.statusPorter == '2' || form.statusParent == '2' || form.statusStudentAffairs == '2' "><span class="status rejected">Rejected</span></td>
                        <td v-else-if="form.statusPorter == '1' && form.statusParent == '1' &&  form.statusStudentAffairs == '1' "><span class="status completed">Approved</span></td>
                        <td v-else-if="form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0' "><span class="status process">Pending</span></td>

                    </tr>
                </tbody>
            </table>
        </div>


        <div style="margin-top:-1.5rem;width:100%;border-top-left-radius: 0;border-top-right-radius: 0;">
            <div class="pagination-row">
                <p v-if='recentFormsFiltered.length<1'>Pages 0 of 0</p>
                <p v-else>Pages {{ recentPage}} of {{pagecount()}}</p>
        
                <div class="items">
                    <button class="pagination-button" :disabled="recentPage<=1" :class="{'disabled':recentPage<=1}"
                        @click="showPages(recentPage-1)">&lt;</button>
        
                    <span v-for="(item, index) in pagecount()" :key="index">
                        <button class="pagination-button" :class="{'active':index+1 == recentPage}"
                            @click="showPages(index+1)">{{ index+1 }}</button>
        
                    </span>
                    <button class="pagination-button" :disabled="recentPage>=pagecount()"
                        :class="{'disabled':recentPage>=pagecount()}" @click="showPages(recentPage+1)">&gt;</button>
                    <slot />
                </div>
            </div>
            <div class="pagination-row" style="display:flex;flex-direction: column;">
                <p>Max items per page:</p>
                <div class="items">
                    <span v-for="num in [5,10,20,25,50]" :key="num">
                        <button class="pagination-button" :class="{'active':num == itemsPerPage}" @click="changeItems(num)">{{
                            num }}</button>
        
                    </span>
                    <slot />
                </div>
            </div>
        </div>


    </div>


    <!--------------------------------------ADMIN------------------------->
    <div v-if="$store.state.usercat === 'superuser' " class="table-data">
        
        <div class="todo" style="overflow-x:hidden !important;">
            <div class="head">
                <h3>Recent Events</h3>
            </div>
            <div style="max-height:25rem; overflow-y:scroll;">
                <ul class="todo-list" v-for="event in events" :key="event">
                    <li v-if="event.tag == 'success'" class="completed" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else-if="event.tag == 'danger'" class="danger" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else class="primary" style="margin-bottom: 1rem;">
                        <div>
                            <small>{{ event.date_added.split('T')[0] }} at {{ event.date_added.split('T')[1].split('.')[0] }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>

                </ul>
            </div>

        </div>
    </div>

</main>
</template>

<script>
import axios from 'axios'

import VSPagination from '@/components/VSPagination.vue'
import DashboardHeader from '@/components/DashboardHeader.vue'
import store from '../store'

import {
    toast
} from 'bulma-toast'

export default {
    name: "Dashboard",
    components: {
        VSPagination,
        DashboardHeader,
    },

    data() {
        return {
            base_URL: axios.defaults.baseURL,
            userdata: '',
            username: this.$store.state.username,
            showForm: false,
            studentDetailPanel: false,
            exeat_type: '',
            departure_date: '',
            arrival_date: '',
            exeat_description_default: '',
            exeat_description_custom: '',
            events: [],
            usercat: store.state.usercat,
            studentDetailList: '',
            recentForms: [],
            recentFormsFiltered: [],
            searchedForms: [],
            searchedtext: '',
            storedFormIds: [],
            descriptions: [],
            desc: '',
            showcustom: false,
            extraTask: false,
            colleges: [],
            extraTask_college: '',
            extraTask_description: '',
            extraTask_command: '',
            extraTask_level: '',
            extraTask_password: '',
            is_in_range: false,
            isSearch: false,
            passCount: 3,
            recentPage: this.$store.state.recentPage || 1,
            itemsPerPage: this.$store.state.itemsPerPage || 5,
            showRequestDetails:false,
            viewform: {},
            
        }
    },

    mounted() {
        localStorage.setItem('isLoading', true)
        
        if(this.$store.state.usercat !== 'superuser'){
            document.title = 'Dashboard | Caleb Exeats' 
            if (this.$store.state.usercat == 'student') {
                
                this.getLocation()
                this.resetCheck()
            }

            this.getDashboardinfo()

            this.timer = setInterval(this.getDashboardinfo, 5000)
           
            
        } else if(this.$store.state.usercat === 'superuser') {
            document.title = 'Caleb Exeats | Administrator';
            
        }
        store.commit('setCurrentTab', '1')
        localStorage.setItem('cT', '1')
        this.timer4 = setInterval(this.checkAuthStat, 500)

        
    },
    methods: {
        async resetCheck(){
            if(this.$store.state.isAuthenticated){
                await axios
                    .get(`api/v1/student/check/${this.$store.state.username}/`)
                    .then(response => {
                        console.log('')
                    })
            }
        },
        
        showPages(pageNo) {     
            const pg = this.itemsPerPage
            const pageCount = this.pagecount()
            if (pageNo > pageCount){
                pageNo = pageCount
            } 
            this.recentFormsFiltered = this.recentForms.slice((pg * pageNo) - pg, pg + ((pg * pageNo) - pg))
            
            console.log(this.recentFormsFiltered)
            
            this.$store.commit('setRecentPage', pageNo)
            this.recentPage = pageNo
        },
        pagecount() {
            return Math.ceil(this.recentForms.length / this.itemsPerPage)
        },
        changeItems(n){
            this.itemsPerPage = n;
            store.commit('setItemsPerPage',n)
            this.showPages(this.recentPage)

        },
        
        greeting() {
            const d = new Date();
            var x = d.getHours();
            if (x >= 16 && x <= 23) {
                return "Good evening";
            } else if (x >= 12 && x < 16) {
                return "Good afternoon";
            } else if (x < 12 && x >= 0) {
                return "Good morning";
            }

        },
        checkAuthStat() {

            if (this.$store.state.isAuthenticated === false || this.$store.state.token == '') {
                this.logout()
                this.cancelFormRefresh()
            }
        },
        removeRequest(form_id) {
            document.getElementById(`pform${form_id}`).style.display = "none"
        },
        SearchedFormsClear() {
            this.searchedForms = []
            this.isSearch = false;
            this.searchedtext = '';
        },

        async getSearchedForms() {
            if (this.$store.state.isAuthenticated) {
                console.table(this.$store.state)
                const s_text = this.searchedtext.replace('/', '-')
                if (s_text.length<6){
                    toast({
                        message: `Invalid Search. Please check the entry and try again`,
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position: 'top-center',
                    })
                } else {
                    await axios
                        .get(`/api/v1/searched-forms/${s_text}=|=${this.$store.state.token}`)
                        .then(response => {
                            //store.commit('setForms',response.data)
                            console.log(response.data)
                            this.searchedForms = response.data
                            this.isSearch = true;

                            if (this.searchedForms.message == 'invalid_search') {
                                toast({
                                    message: `Invalid Search. Please check the entry and try again`,
                                    type: 'is-danger',
                                    dismissible: true,
                                    pauseOnHover: 'true',
                                    duration: 3000,
                                    position: 'top-center',
                                })
                            } else if (this.searchedForms.length < 1 && this.isSearch == true) {
                                toast({
                                    message: `No Requests Found!`,
                                    type: 'is-primary',
                                    dismissible: true,
                                    pauseOnHover: 'true',
                                    duration: 3000,
                                    position: 'top-center',
                                })
                            }
                        })
                        .catch(error => {
                            console.log("and error occured")
                        })
                }
            }
        },
        cancelFormRefresh() {
            clearInterval(this.timer);
            clearInterval(this.timer4);
        },

        async getDashboardinfo() {
            
            if (this.$store.state.isAuthenticated) {
                await axios
                    .get(`/api/v1/user-data/${this.$store.state.token}`)
                    .then(response => {
                        if (response.data.message == 'user_not_exist') {
                            this.logout()
                        } else {
                            store.commit('setUserdata', response.data)
                            this.userdata = response.data
                
                        }
                        
                    })
                    .catch(error => {
                        console.log("and error occured")
                    })


                if (this.$store.state.usercat == 'saffairs' || this.$store.state.usercat == 'porter') {
                    await axios
                        .get(`/api/v1/recent-forms/forced/${this.$store.state.token}=|=0`)
                        .then(response => {
                            localStorage.setItem('isLoading', false)
                            //store.commit('setForms',response.data)
                            this.recentForms = response.data
                            if (this.recentForms.length < 1) {
                                toast({
                                    message: `No Forms Yet`,
                                    type: 'is-dark',
                                    dismissible: true,
                                    pauseOnHover: 'true',
                                    duration: 5000,
                                    position: 'top-center',
                                })
                                this.cancelFormRefresh();
                            }
                            this.showPages(this.recentPage)
                        })
                        .catch(error => {
                            toast({
                                message: `Could not connect to server. Please check your connection`,
                                type: 'is-danger',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 5000,
                                position: 'top-center',
                            })
                            this.cancelFormRefresh();
                        })
                } else if (this.$store.state.usercat == 'student' || this.$store.state.usercat == 'security' || this.$store.state.usercat == 'parent') {
                    await axios
                        .get(`/api/v1/recent-forms/${this.$store.state.token}`)
                        .then(response => {
                            localStorage.setItem('isLoading', false)
                            //store.commit('setForms',response.data)
                            this.recentForms = response.data
                            if (this.$store.state.usercat == 'security') {
                                this.showPages(this.recentPage)
                            }
                            
                        })
                        .catch(error => {
                            toast({
                                message: `Could not connect to server. Please check your connection`,
                                type: 'is-danger',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 5000,
                                position: 'top-center',
                            })
                            this.cancelFormRefresh();
                        })
                }

                if (this.$store.state.usercat != 'security') {
                    await axios
                        .get(`/api/v1/notification/${this.$store.state.token}/`)
                        .then(response => {
                            //store.commit('setEvents',response.data)
                            console.log('eevents', response.data)
                            this.events = response.data.slice(0, 10)
                        })
                        .catch(error => {
                            toast({
                                message: `Could not connect to server. Please check your connection`,
                                type: 'is-danger',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 5000,
                                position: 'top-center',
                            })
                            this.cancelFormRefresh();

                        })

                }
                await axios
                    .get("/api/v1/form/descriptions/")
                    .then(response => {
                        this.descriptions = response.data
                        console.log(this.descriptions)
                    })
                    .catch(error => {
                        toast({
                            message: `Could not connect to server. Please check your connection`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 5000,
                            position: 'top-center',
                        })
                    })

            }
        },

        async approveRequest(formid, matric_no) {
            if (this.$store.state.isAuthenticated) {
                this.removeRequest(formid)
                const formData = {
                    username: this.$store.state.username,
                    token: this.$store.state.token,
                    formpk: formid,
                    change: "approve",
                    matric_no: matric_no,
                }
                await axios
                    .post('api/v1/form/change/', formData)
                    .then(response => {
                        if (response.data.message == "success") {
                            toast({
                                message: `Form was Approved Successfully`,
                                type: 'is-success',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 3000,
                                position: 'top-center',
                            })
                        } else {
                            document.getElementById(`pform${formid}`).style.display = "";
                            toast({
                                message: `Form Approval Error`,
                                type: 'is-danger',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 3000,
                                position: 'top-center',
                            })

                        }

                    })
                    .catch(error => {
                        console.log('error')
                        document.getElementById(`pform${formid}`).style.display = "";
                        toast({
                            message: `Form Approval Error`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-center',
                        })
                    })

                    this.showPages(this.recentPage);
            } else {
                this.logout()
            }

        },
        async rejectRequest(formid, matric_no) {
            this.removeRequest(formid)

            const formData = {
                username: this.$store.state.username,
                token: this.$store.state.token,
                formpk: formid,
                change: "reject",
                matric_no: matric_no,
            }
            await axios
                .post('api/v1/form/change/', formData)
                .then(response => {
                    if (response.data.message == "success") {
                        toast({
                            message: `Form was Rejected Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-center',
                        })
                    } else {
                        document.getElementById(`pform${formid}`).style.display = "";
                        toast({
                            message: `Form Rejection Error`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-center',
                        })

                    }

                })
                .catch(error => {
                    console.log('error')
                    document.getElementById(`pform${formid}`).style.display = "";
                    toast({
                        message: `Form Rejection Error`,
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position: 'top-center',
                    })
                })

                this.showPages(this.recentPage);

        },

        async cancelForm(form_id) {
            const data = {
                form_id: form_id,
                token: this.$store.state.token,
                username: this.$store.state.username
            }
            await axios
                .post('/api/v1/form/cancel-form/', data)
                .then(response => {
                    if (response.data == "success") {
                        toast({
                            message: `Form Cancelled Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'bottom-right',
                        })
                    }
                })
        },

        async showDetails(mat_no) {
            this.studentDetailList=''
            this.studentDetailPanel = true;
            await axios
                .get(`api/v1/students-data/${this.$store.state.username}=|=${mat_no}`)
                .then(response => {
                    this.studentDetailList = response.data
                })
                .catch(error => {
                    console.log("an error occured ")
                })

        },

        checkStatus(value) {
            if (value == '2') {
                return "rejected"
            } else if (value == '1') {
                return "approved"
            }
            return "pending"
        },
        valiDate(){
            return true
        
        },

        async sendRequest(){
            if (this.$store.state.userdata.forms_left == 0){
                toast({
                        message: `Daily Request limit Exceeded`,
                        type: 'is-dark',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position: 'top-center',
                    })
            } else {
                if(this.is_in_range){
                    if(this.showcustom || this.exeat_type == 'short'){
                        var description = this.exeat_description_custom
                    } else {
                        var description = this.exeat_description_default
                    }

                    if(this.exeat_type == 'short'){
                        this.arrival_date == this.departure_date
                    }

                    if(description != '' && this.exeat_type != '' && this.departure_date != '' && this.arrival_date != ''){
                        const data = {
                            token:this.$store.state.token,
                            exeat_type:this.exeat_type,
                            description:description,
                            matric_no:this.$store.state.username,
                            departure_date:this.departure_date,
                            arrival_date:this.arrival_date
                        }
                        this.showForm = false
                        await axios
                            .post('api/v1/form/create/', data)
                            .then(response => {
                                if(response.data.message == 'success'){
                                    toast({
                                        message: `Request sent Successfully`,
                                        type: 'is-success',
                                        dismissible: true,
                                        pauseOnHover: 'true',
                                        duration: 3000,
                                        position: 'top-center',
                                    })
                                    this.getDashboardinfo();

                                } else if (response.data.message == 'daily_form_limit_exceeded'){
                                    toast({
                                        message: `Daily Form Limit Exceeded`,
                                        type: 'is-dark',
                                        dismissible: true,
                                        pauseOnHover: 'true',
                                        duration: 3000,
                                        position: 'top-center',
                                    })
                                } else if (response.data.message == 'invalid_authentication'){
                                    toast({
                                        message: `Invalid Authentication`,
                                        type: 'is-danger',
                                        dismissible: true,
                                        pauseOnHover: 'true',
                                        duration: 3000,
                                        position: 'top-center',
                                    })
                                    this.logout();
                                }
                            })
                    } else {
                        toast({
                            message: `Complete All Fields !`,
                            type: 'is-dark',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-center',
                        })
                    }

                } else {
                    toast({
                            message: `Not within Range.Please try again later ! `,
                            type: 'is-dark',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'top-center',
                        })
                }

            

            }
            
            


            
        },
        customTask() {
            if (this.extraTask_college == '' || this.extraTask_description == '' || this.extraTask_command == '' || this.extraTask_level == '') {
                alert('Enter All Fields')
                toast({
                    message: `Complete All Inputs`,
                    type: 'is-danger',
                    dismissible: true,
                    pauseOnHover: 'true',
                    duration: 3000,
                    position: 'bottom-right',
                })

            } else {
                const bulkData = {
                    command: this.extraTask_command,
                    college: this.extraTask_college,
                    level: this.extraTask_level,
                    description: this.extraTask_description,
                    password: this.extraTask_password,
                    usercat: this.$store.state.usercat,
                    username: this.$store.state.username,
                    token: this.$store.state.token,
                }

                console.table(bulkData)

                axios
                    .post('api/v1/actions/customTask/', bulkData)
                    .then(response => {
                        console.log(response.data)
                        if (response.data.message == 'incorrect_password') {
                            this.extraTask_password = '';
                            this.passCount -= 1;
                            if (this.passCount == 0) {
                                const token = this.$store.state.token
                                axios.defaults.headers.common["authorization"] = ""

                                localStorage.removeItem("token")
                                localStorage.removeItem("username")
                                localStorage.removeItem("userid")
                                localStorage.removeItem('cT')
                                this.$store.commit('removeToken')

                                axios
                                    .post('api/v1/person/logout/', {
                                        token: token
                                    }, )
                                    .then(response => {
                                        toast({
                                            message: `Logged out due to multiple incorrect enteries. Please type the correct information or contact an Administrator`,
                                            type: 'is-info',
                                            dismissible: true,
                                            pauseOnHover: 'true',
                                            duration: 5000,
                                            position: 'top-right',
                                        })

                                    })

                                this.$router.push('/login')
                            } else {
                                toast({
                                    message: `USER Password was Incorrect. ${this.passCount} Attempts remaining`,
                                    type: 'is-danger',
                                    dismissible: true,
                                    pauseOnHover: 'true',
                                    duration: 5000,
                                    position: 'top-center',
                                })

                            }

                        } else if (response.data.message == 'completed_successfully') {
                            toast({
                                message: `Completed successfully, Executing changes in the background`,
                                type: 'is-success',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 6000,
                                position: 'top-center',
                            })

                            this.extraTask = false;
                            this.extraTask_password = '';
                            this.extraTask_college = 'Select college';
                            this.extraTask_command = 'Select a Command';
                            this.extraTask_description = 'Select Description';
                            this.extraTask_level = 'Select Level';
                        }

                    })
            }

        },

        getLocation() {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(this.showPosition, this.showError);
            } else { 
                toast({
                    message: `Geolocation is not supported by this browser. Please try again using another browser.`,
                    type: 'is-primary',
                    dismissible: true,
                    pauseOnHover: 'true',
                    duration: 3000,
                    position: 'bottom-right',
                })
            }
        },
        deg2rad(deg) {
            return deg * (Math.PI / 180)
        },
        showPosition(position) {
            const rangeValue = 0.4528483801332947;
            //const rangeValue = 1000.4528483801332947;
            const lat1 = position.coords.latitude;
            const lon1 = position.coords.longitude;
           
            const lat2 = 6.669883
            const lon2 = 3.637827

            var R = 6371; // Radius of the earth in km
            var dLat = this.deg2rad(lat2 - lat1);  // deg2rad below
            var dLon = this.deg2rad(lon2 - lon1);

            var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) * Math.sin(dLon / 2) * Math.sin(dLon / 2);

            var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
            var d = parseFloat(R * c); // Distance in km



            //CHECK IF DISTANCE IS WITHIN MAX RANGE
            console.log(d)
            if (parseFloat(d) <= parseFloat(rangeValue)) {
                this.is_in_range = true;
                
            } else {
                toast({
                        message: "You're not within the school's range so you are unable to send or edit requests",
                        type: 'is-dark',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 10000,
                        position: 'bottom-right',
                    })
                
                this.is_in_range = false;
               
            }
            


        },
        showError(error) {
            switch(error.code) {
                case error.PERMISSION_DENIED:
                    toast({
                            message: `You denied the request for geolocation or disabled location access. Please enable location access to continue.`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position: 'bottom-right',
                        })
                break;
                case error.POSITION_UNAVAILABLE:
                    console.log('unable to verify location')
                break;
                case error.TIMEOUT:
                    toast({
                        message: `Location verification timed out`,
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position: 'bottom-right',
                    })
                break;
                case error.UNKNOWN_ERROR:
                    toast({
                        message: `An unknown error occured. Please try again later`,
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position: 'bottom-right',
                    })
                break;
            }
        },


        logout() {

            const token = this.$store.state.token
            axios.defaults.headers.common["authorization"] = ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")
            localStorage.removeItem('cT')
            this.$store.commit('removeToken')
            axios
                .post('api/v1/person/logout/', {
                    token: token
                }, )
                .then(response => {
                    toast({
                        message: `Logged out successfully`,
                        type: 'is-info',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 5000,
                        position: 'top-right',
                    })

                })

            this.$router.push('/login')

        },

    },
    beforeUnmount() {
        this.cancelFormRefresh()
    }
}
</script>

