<template>
  
  <div class="wrapper" :class="{'dark' :$store.state.showDark }">
	<div class="loadingbar" :class="{'showloader':isLoading}">
		<div class="loader"></div>
	</div>
    <!-- SIDEBAR -->
	<section v-if="$store.state.isAuthenticated" id="sidebar" v-bind:class="{'hide': hideSidebar}">
		<a href="#" v-if="hideSidebar == true " class="brand">
			<img src="./assets/school-logo.png" alt="" style="width:40px;height:40px;margin:1rem">
			
		</a>
		<a href="#" v-if="hideSidebar == false" class="brand">
			<img src="./assets/logo-dark.png" alt="" style="width:180px;height:45px;margin:1rem">
		
		</a>
		<ul class="side-menu top">
			<router-link to="/">
					<li :class="{'active': $store.state.currentTab=='1' }" @click="setCurrent('1')">
			
					<a href="#">
					<i class='material-icons'>dashboard</i>
					<span title="Dashboard" class="link">Dashboard</span>
						  </a>
					</li>
			</router-link>
			
			<router-link to="/users">
			<li v-if="$store.state.usercat === 'superuser' " :class="{'active': $store.state.currentTab=='2' }" @click="setCurrent('2')">
					<a href="#">
						<i class='material-icons'>groups</i>
				<span class="link">Manage Users</span>
					</a>
			</li>
			</router-link>
			
			<router-link to="/events">
			<li v-if="$store.state.usercat!='student' && $store.state.usercat!='security' || $store.state.usercat === 'superuser' " :class="{'active': $store.state.currentTab=='3' }" @click="setCurrent('3')">	
					<a href="#">
						<i class='material-icons'>event_note</i>
				<span class="link">Events</span>
					</a>	
			</li>
			</router-link>

			<router-link to="/tickets">
			<li v-if="$store.state.usercat === 'superuser' " :class="{'active': $store.state.currentTab=='4' }" @click="setCurrent('4')">
				
					<a href="#">
						<i class='material-icons'>local_activity</i>
				<span class="link">Tickets</span>
					</a>
			</li>
			</router-link>

			<router-link to="/history">
			<li v-if="$store.state.usercat!='student' && $store.state.usercat!='security' || $store.state.usercat === 'superuser' " :class="{'active': $store.state.currentTab=='5' }" @click="setCurrent('5')">
					<a href="#">
						<i class='material-icons'>history</i>
				<span class="link">History</span>
					</a>
			</li>
			</router-link>

			<router-link to="/profile">
			<li v-if="$store.state.usercat=='student' || $store.state.usercat=='parent'" :class="{'active': $store.state.currentTab=='6' }" @click="setCurrent('6')">
					<a href="#">
						<i class='material-icons'>account_circle</i>
						<span class="link">Profile</span>
							  </a>
			</li>
			</router-link>

			<router-link to="/settings">
			<li v-if="$store.state.usercat==='superuser'" :class="{'active': $store.state.currentTab=='7' }" @click="setCurrent('7')">
					<a href="#">
						<i class='material-icons'>settings</i>
						<span class="link">Settings</span>
							  </a>
			</li>
			</router-link>

		</ul>
		<ul class="side-menu">
			
			<li @click="logout()">
				<a href="#" class="logout">
          <i class='material-icons'>logout</i>
          <span class="link">Logout</span>
				</a>
			</li>
		</ul>
	</section>
	<!-- SIDEBAR -->


	<div class="modal" :class="{'showform' :addimageform }">
		<form class="modal-content">
			<div class="xbar">
                <a href="#"  @click="addimageform = false"><i class='material-icons'>close</i></a> 
             </div>

			 <div class="image-field">
				<img :src="$store.state.profilePic" alt="" style="width:5rem;height:5rem;border-radius:50%">
			 </div>
			 
			 <div class="field">
				<p style="margin-left:.5em;padding-bottom:2em;">Add/Edit Profile Image (file should not be more than {{imageUploadSize}}MB )</p>
				<div class="progress" v-if="document"><progress :value="progress" max="100"></progress><span style="margin-left:1rem;color:var(--dark)">{{ progress }}%</span></div>
				
				<input type="file" accept="image/*" ref="file" style="border:none;margin-top:-1em;"  @change="selectFile">
			 </div>

			 
			 <a v-if="document" href="#" @click="uploadImage" style="background-color:var(--blue);color:var(--light);padding:.5rem .5rem;font-size:1rem;border-radius:.2rem">Upload Image</a>

		</form>
	</div>





  
  <section id="content">
		<!-- NAVBAR -->
		<nav id="navbar" v-if="$store.state.isAuthenticated">
			<i v-if="hideSidebar" class="material-icons" style="margin-top:.2rem;color:var(--dark);" @click="hideSidebar = !hideSidebar">menu</i>
			<i v-else class="material-icons" style="margin-top:.2rem;color:var(--dark);" @click="hideSidebar = !hideSidebar">menu_open</i>
			
			<div id="alignbuttons">
				<input type="checkbox" id="switch-mode" hidden @click="changeDark()">
				<label for="switch-mode" class="switch-mode"></label>
				<div href="#" class="profile" @click="addimageform = !addimageform" v-if="$store.state.profilePic">
					<img id="profileImage" v-if="$store.state.usercat!='security' && $store.state.usercat!='superuser'" :src="$store.state.profilePic">
					
				</div>
				<div href="#" class="profile" @click="addimageform = !addimageform" v-else>
					<img v-if="$store.state.usercat=='security' || $store.state.usercat=='superuser'" src="./assets/profile.png">
			
				</div>
			</div>
			
		</nav>
		<!-- NAVBAR -->

		<!-- MAIN -->
		<router-view />
		<!-- MAIN -->
	</section>
	<!-- CONTENT -->
  </div>
  
</template>

<script>
import { throwStatement } from '@babel/types'
import axios from 'axios';

import { ref } from 'vue';

import store from './store';

import { toast } from 'bulma-toast';

export default {
    data() {
      return{
        showMobileMenu:false,
        hideSidebar:true,
		currentTab:localStorage.getItem('cT'),//this.$store.state.currentTab,
		message:'',
		document:null,
		progress:0,
		profilePic:this.$store.state.profilePic,
		addimageform:false,
		username: store.state.username,
		defaultImage:'./assets/profile.png',
		isLoading:false,
		imageUploadSize:2,
		
		
      }
    },
	beforeCreate(){
		this.$store.commit('initializeStore')

		const token = this.$store.state.token
   
		if (token) {
			axios.defaults.headers.common['Authorization'] = "Token " + token
		} else {
			axios.defaults.headers.common['Authorization'] = ""
		}


	},

	mounted(){

		this.timer = setInterval(this.checkLoad,'500')
		
		if(store.state.currentTab == ""){
			this.setCurrent("1")
		}
		if(localStorage.getItem('cT') == null){
			localStorage.setItem('cT','1')
		}
		
		if(this.$store.state.isAuthenticated && this.$store.state.usercat != 'superuser'){
			this.updatestore()
			this.getDashboardinfo()
		}
		
		
		
		
	
		
	
		

	},
	methods: {
		async checkLoad(){
			if(JSON.parse(localStorage.getItem('isLoading'))===true){
				 this.isLoading = true
			} else {
				this.isLoading = false
			}
		},
		selectFile() {
			this.document = this.$refs.file.files.item(0)
			if(this.document.size > (1048576 * this.imageUploadSize)){
				this.document = null;
				toast({
						message: `Image file too Large. Image must not be more than ${this.imageUploadSize}MB`,
						type: 'is-danger',
						dismissible: true,
						pauseOnHover: 'true',
						duration: 5000,
						position:'top-center',
					})

			}
		},
		uploadImage(){
			this.progress = 0;

			this.performUpload(this.document, event => {
				this.progress = Math.round((100 * event.loaded) / event.total)
			})
			.then(response => {
				this.message="Image uploaded successfully";
				this.document=null
			})
			.catch(error => {
				this.progress=0;
				this.message = "something went wrong, please try again!";
				this.document=null
			})

		},
		async performUpload(file, onuploadProgress) {
			let formData = new FormData();
			formData.append('image', file)
			
			//formData.append('token', this.$atore.state.token)
			console.log(formData.get('image'))
			console.log('ejk')
			console.log(file)

			return axios
				.post('/api/v1/upload/', {token:this.$store.state.token,formdata:formData.get('image')}, {
					headers: { 
						"Content-Type":"multipart/form-data",

					},
					onuploadProgress

				})
				.then(response => {
					console.log(response.data)
					this.progress = 100;

					this.addimageform = false;
					toast({
                            message: `Image Updated Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-center',
                        })
						
					this.document = null
					this.updatestore()
				})

		},
		
		
		async updatestore(){
			if(this.$store.state.usercat != 'superuser'){
				await axios
				.get(`/api/v1/user-data/${this.$store.state.token}`)
				.then(response => {
					const profilepic = `${axios.defaults.baseURL}${response.data.image}`
					const fullname = `${response.data.lastname} ${response.data.firstname} ${response.data.middlename}`
					const username = response.data.username
					const hostel = response.data.hostel
					const usercat = response.data.usercat

					store.commit('setUsername', username)
					store.commit('setFullname', fullname)
					store.commit('setHostel', hostel)
					store.commit('setUsercat', usercat)



					if(response.data.image != null){
						store.commit('setProfilePic', profilepic)

					} else {
						store.commit('setProfilePic','./assets/profile.png')

					}
					
					

				

					

				})
				.catch( error => {
					console.log("and error occured")
				})
			} else {

			}
		},

			async getDashboardinfo(){
				if (store.state.isAuthenticated && this.$store.state.usercat != 'superuser'){
					await axios
						.get(`/api/v1/user-data/${this.$store.state.token}`)
						.then(response => {
								store.commit('setUserdata',response.data)
								this.profilePicture=`${axios.defaults.baseURL}${response.data.image}`

						})
						.catch( error => {
							console.log("and error occured")
						})
				}
			},

			async getRecentEvents(){
				if (store.state.isAuthenticated){
	
					await axios 
						.get(`/api/v1/events/${this.username}`)
						.then(response => {
							store.commit('setEvents',response.data)
						})
				}
			},

			
		changeDark(){
			store.commit('changeDark')
		},
		setCurrent(tab){
			store.commit('setCurrentTab',tab)
			localStorage.setItem('cT',tab)
			this.currentTab = tab
		},

		async logout(){

			const token = this.$store.state.token
			axios.defaults.headers.common["authorization"] = ""

			localStorage.removeItem("token")
			localStorage.removeItem("username")
			localStorage.removeItem("userid")
			localStorage.removeItem('cT')
			this.$store.commit('removeToken')
			await axios
				.post('api/v1/person/logout/',{token:token},)
				.then(response => {
					toast({
                            message: `${response.data.message}`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-right',
                        })
					
				})

			
			this.$router.push('/login')
			
		},

	},
	beforeUnmount(){
		clearInterval(this.timer)
	},	
	unmounted(){
		
		this.$store.commit('removeToken')
		
        
	}

  } 
</script>



