import { createStore } from 'vuex'

//import { ref, computed } from "vue";
//import { defineStore } from "pinia";


import createPersistedState from "vuex-persistedstate"

export default createStore({
    strict: process.env.NODE_ENV !== 'production',
    state: {
        isAuthenticated: false,
        token: '',
        username: '',
        fullName: '',
        hostel: '',
        usercat: '',
        profilePic: '',
        showDark: false,
        userdata: '',
        notifications: [],
        currentTab: '',
        recentPage: 1,
        itemsPerPage: 5,
        events: [],
        recentForms: [],
        recentFormsFiltered: [],
        UAD:'',
        

    },
    getters: {},
    mutations: {
        initializeStore(state) {
            if (localStorage.getItem('token')) {
                state.token = localStorage.getItem('token')
            } else {
                state.token = ''
                state.isAuthenticated = false
            }

        },

        setForms(state, forminput) {
            state.recentForms = forminput
        },
        setFilterForm(state, forminput) {
            state.recentFormsFiltered = forminput
        },
        setEvents(state, eventinput) {
            state.events = eventinput
        },

        setUserdata(state, usip) {
            state.userdata = usip
        },
        setUserDataAdmin(state, data) {
            state.UAD = data
        },

        changeDark(state) {
            state.showDark = !state.showDark
        },
        setRecentPage(state, page) {
            state.recentPage = page
        },
        setItemsPerPage(state, items) {
            state.itemsPerPage = items
        },

        setToken(state, token) {
            state.token = token
            state.isAuthenticated = true
        },
        setAdminData(state, adminData) {
            state.adminData = adminData

        },




        setUsername(state, username) {
            state.username = username
        },

        setFullname(state, fullname) {
            state.fullName = fullname
        },

        setHostel(state, hostel) {
            state.hostel = hostel
        },

        setUsercat(state, usercat) {
            state.usercat = usercat
        },

        setCurrentTab(state, tab) {
            state.currentTab = tab
        },
        setProfilePic(state, profilePic) {
            state.profilePic = profilePic
        },




        appendNotifications(state, notification) {
            state.notifications.push(notification)
        },

        removeToken(state) {
            state.token = ''
            state.isAuthenticated = false
            state.username = ''
            state.usercat = ''
            state.hostel = ''
            state.fullName = ''
            state.profilePic = ''
            state.UAD=''
            state.currentTab = ""
            state.recentForms = []
            state.recentFormsFiltered = []
            state.events = []
            state.notifications = []
            state.userdata = ""
            state.notifications = []
            state.recentPage = 1
            state.itemsPerPage = 10

        },



    },
    actions: {},
    modules: {},
    plugins: [createPersistedState()]
})



