<template>
  <main>
    <div class="table-data" >




        <div class="todo" style="overflow-x:hidden !important;">
            <div style="margin-bottom:2rem ;">
                <router-link to="/" style="background-color:var(--blue); color:var(--light); padding:0.2rem 0.5rem; border-radius:.2rem;">
                    back
                </router-link>
            </div>



            <div style="margin-top:2rem;width:100%;border-top-left-radius: 0;border-top-right-radius: 0;">
                <p v-if='recentEventsFiltered.length<1'>Pages 0 of 0</p>
                <p v-else>Pages {{ recentPage}} of {{pagecount()}}</p>
                    
                <div class="pagination-row" style="overflow-y:scroll;max-height:5rem;">
                    
                    <div class="items">
                        <button class="pagination-button" :disabled="recentPage<=1" :class="{'disabled':recentPage<=1}" @click="showPages(recentPage-1)">&lt;</button>
                        
                    <span v-for="(item, index) in pageNoarr" :key="index">
                        <button class="pagination-button" :class="{'active':item == recentPage}" @click="showPages(item)">{{ item }}</button>
                        
                    </span>
                    <span v-if="pagecount()>itemMax && pageNoarr.indexOf(pagecount()) == -1">...</span>
                    <button class="pagination-button" :disabled="recentPage>=pagecount()" :class="{'disabled':recentPage>=pagecount()}" @click="showPages(recentPage+1)">&gt;</button>
                    <slot />
                    </div>
                </div>
                
            </div>



            <div class="head">
                
                <h3>All Events</h3>
                
            </div>

             


            <div class="sorting">
               <p> sort by:</p>
               <div class="options">
                    <input type="checkbox">
                    <label for="">All</label>
               </div>
               <div class="options">
                    <input type="checkbox">
                    <label for="">Approved</label>
               </div>
               <div class="options">
                    <input type="checkbox">
                    <label for="">Rejected</label>
               </div>
               <div class="options">
                    <input type="checkbox">
                    <label for="">Pending</label>
               </div>
               <div class="options">
                    <input type="checkbox">
                    <label for="">Important</label>
               </div>
            </div>
            <div style="max-height:40rem; overflow-y:scroll;">
                <ul class="todo-list" v-for="event in recentEventsFiltered" :key="event" >
                    <li v-if="event.tag == 'success'"  class="completed" style="margin-bottom: 1rem;">
                        <div> 
                            <small>{{ event.date_added_main }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else-if="event.tag == 'danger'"  class="danger" style="margin-bottom: 1rem;">
                        <div> 
                            <small>{{ event.date_added_main }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    <li v-else class="primary" style="margin-bottom: 1rem;">
                        <div> 
                            <small>{{ event.date_added_main }}</small>
                            <p>{{ event.notification }}</p>
                        </div>
                    </li>
                    
                </ul>
            </div>
           
        </div>


        
    </div>
    

  </main>
</template>

<script>
import axios from 'axios'



import { toast } from 'bulma-toast'

export default {
    name:"Notifications",
    data(){
        return{
            events: [],
            recentEventsFiltered: [],
            username: this.$store.state.username,
            recentPage: parseInt(localStorage.getItem('recentEventPage')) || 1,
            itemsPerPage: parseInt(localStorage.getItem('itemsPerEventPage')) || 10,
            pageNoarr:[],
            itemMax:6,
        }
    },
    mounted(){
        this.getRecentEvents()
        localStorage.setItem('cT','2')
        this.timer4 = setInterval(this.checkAuthStat, 1000)
        this.pageflex(this.recentPage)
    },
    beforeUnmount(){
        clearInterval(this.timer4)
    },
    methods:{
        pageflex(pageno){
            console.log('pageflex')
            var pagearr = []
            var pageCount = this.pagecount()
            if(pageno<this.itemMax-3){
                
                if(pageCount > this.itemMax){
                    for(let x=1;x<=this.itemMax;x++){
                        pagearr.push(x)
                    }
                } else {
                    for(let x=1;x<=pageCount;x++){
                        pagearr.push(x)
                    }
                }
            } else {
                for(let x=1;x<=this.itemMax;x++){
                        pagearr.push(x)
                }
                if(pageno>pageCount-3){
                    var npage = pageCount
                } else {
                    var npage = (pageno+3)
                }
                for(let x=this.itemMax+1;x<=npage;x++){
                   if(x<=this.pagecount()){
                     pagearr.push(x)
                   } else {
                        break;
                   }
                }
                
                pagearr = pagearr.slice(npage-this.itemMax)

            }

            console.log(pagearr)
            this.pageNoarr = pagearr
           
            console.log(this.pageNoarr)
        },
        checkAuthStat() {

            if (this.$store.state.isAuthenticated === false || this.$store.state.token == '') {
                this.logout()
            }
        },
        async logout() {

            const token = this.$store.state.token
            axios.defaults.headers.common["authorization"] = ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")
            localStorage.removeItem('cT')
            this.$store.commit('removeToken')
            await axios
                .post('api/v1/person/logout/', {
                    token: token
                }, )
                .then(response => {
                    toast({
                        message: `Logged out successfully`,
                        type: 'is-info',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 5000,
                        position: 'top-right',
                    })

                })

            this.$router.push('/login')

        },
        async getRecentEvents(){
            if (this.$store.state.isAuthenticated){

                await axios 
                    .get(`/api/v1/notification/${this.$store.state.token}`)
                    .then(response => {
                        //store.commit('setEvents',response.data)
                        this.events=response.data
                        this.showPages(this.recentPage)
                    })
                }
        },
        showPages(pageNo) {     
            this.pageflex(pageNo)
            const pg = this.itemsPerPage
            const pageCount = this.pagecount()
            if (pageNo > pageCount){
                pageNo = pageCount
            } 
            this.recentEventsFiltered = this.events.slice((pg * pageNo) - pg, pg + ((pg * pageNo) - pg))
            
            //console.log(this.recentEventsFiltered)
            
            localStorage.setItem('recentEventPage', pageNo)
            this.recentPage = pageNo
            
        },
        pagecount() {
            return Math.ceil(this.events.length / this.itemsPerPage)
        },
        changeItems(n){
            this.itemsPerPage = n
            localStorage.setItem('itemsPerEventPage', parseInt(n))
            this.showPages(this.recentPage)
            this.pageflex(this.recentPage)
        },
    }
}
</script>

<style>
.sorting{
    display:none;
    border:2px solid var(--grey);
    margin-bottom:2rem;
    padding:.5rem;
    border-radius: .5rem;
}
   
    .sorting .options{
        padding:0 1rem;
        width:3 5rem;
    }
    .sorting .options label{
        margin-left:.5rem;
    }

    .sorting .options input{
        width:1rem;
        height:1rem;
    }
  

</style>