<template>
  <main>
    
    <div v-if="usercat == 'student'" class="table-data">
        <div style="margin-bottom:-4rem; padding:1rem;width:100%">
            <router-link to="/" >
                back
            </router-link>
            <div class="head" v-if="this.recentForms.length > 1">
                
                <h3 style="margin:1rem 0">All Requests</h3>
            </div>
            <div class="head" v-else-if="this.recentForms.length < 1">
                <h3 style="font-size:16px;">No Recent Requests Yet!. <p style="color:#aaa">click the button on the top of the page to create one!</p></h3>
            </div>
        </div>
        <div class="order" style="overflow-y: scroll; max-height:50rem;">
            
            <table>
                <thead>
                    <tr>
                        <th style='display:none'></th>
                        <th style='display:none'></th>
                        <th>Form ID</th>
                        <th>Exeat Type</th>
                        <th>Date Sent</th>
                        <th style="padding:0rem 15rem 0rem 1rem;text-align:left;">Description</th>
                        <th>Departure Date</th>
                        <th>Arrival Date</th>
                        <th>porter's Approval</th>
                        <th>parent's Approval</th>
                        <th>s.A. Approval</th>
                        <th>Final Remarks</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="form in recentForms" :key="form.id">
                       <td style='display:none'></td>
                       <td style='display:none'></td>
                        <td class="fix">CULEXT{{ form.id }}</td>
                        <td>{{ form.exeat_type }}</td>
                        <td>{{ form.date_added }}</td>
                        
                        <td>{{ form.exeat_description }}</td>
                        <td>{{ form.departure_date }}</td>
                        <td>{{ form.arrival_date }}</td>
                     

                        <td v-if="form.statusPorter == '0'"><span class="status process">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>
                       
                        
                        <td v-if="form.statusParent == '0'"><span class="status process">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>
                       
                        
                        <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                       
                        <td v-if="form.iscancelled"><span class="status rejected">Cancelled</span></td>
                        <td v-else-if="form.statusPorter == '2' || form.statusParent == '2' || form.statusStudentAffairs == '2' "><span class="status rejected">Rejected</span></td>
                        <td v-else-if="form.statusPorter == '1' && form.statusParent == '1' &&  form.statusStudentAffairs == '1' "><span class="status completed">Approved</span></td>
                        <td v-else-if="form.statusPorter == '0' || form.statusParent == '0' || form.statusStudentAffairs == '0' "><span class="status process">Pending</span></td>
                       </tr>
                </tbody>
            </table>
        </div>

    </div>




    <div v-if="usercat == 'parent'" class="table-data">
            
        <div class="order"  style="overflow-y: scroll; max-height:40rem;">
            
            <div class="head" v-if="this.recentForms.length > 1">
                <span>
                    <h3 style="margin-left:2rem !important">Requests History</h3>
                
                </span>
            </div>

            <div class="head" v-else-if="this.recentForms.length < 1 ">
                <h3 style="margin-left:2rem !important">No Pending Requests Yet!</h3>
            </div>
            <table>
                <thead>
                    <tr>
                        
                        <th class="keep-on-top" style="padding-left:2rem !important;">Form ID</th>
                        <th style="width:1500px !important;">Fullname</th>
                        <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Matric No.</th>
                        
                  
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Description</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Duration</th>
                
                        <th>porter's Approval</th>
                        <th>S.A. Approval</th>
                        
                        <th>parent's Approval</th>
                     
                    </tr>
                </thead>
                <tbody v-for="form in recentForms" :key="form.id">
                    <tr :id="`pform${form.id}`">
                        
                        <td>CULEXT{{ form.id }}</td>
        
                        <td>{{ form.fullname }}</td>
                        <td>{{ form.matric_no }}</td>
                        <td>{{ form.date_added_main }}</td>
                        
                        <td >{{ form.exeat_description }}</td>
                        <td>{{ form.departure_date }} to {{ form.arrival_date }}</td>
               
                        
                        <td v-if="form.statusPorter == '0'"><span class="status process">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                        <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>
                       
                        
                        
                        <td v-if="form.statusStudentAffairs == '0'"><span class="status process">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '1'"><span class="status completed">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                        <td v-else-if="form.statusStudentAffairs == '2'"><span class="status rejected">{{ checkStatus(form.statusStudentAffairs) }}</span></td>
                       
                       
                        <td v-if="form.statusParent == '0'"><div class="choice"><button class="approve" @click="this.approveRequest(form.id, form.matric_no)">Approve Request</button><button class="reject" @click="rejectRequest(form.id, form.matric_no)">Reject Request</button></div></td>
                        <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                        <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>
                       

                    </tr>
                </tbody>
            </table>
        </div>



       
    </div>



