                            <td v-else-if="form.statusParent == '1'"><span class="status completed">{{ checkStatus(form.statusParent) }}</span></td>
                            <td v-else-if="form.statusParent == '2'"><span class="status rejected">{{ checkStatus(form.statusParent) }}</span></td>
                           
                            
