                            <td v-else-if="form.statusPorter == '1'"><span class="status completed">{{ checkStatus(form.statusPorter) }}</span></td>
                            <td v-else-if="form.statusPorter == '2'"><span class="status rejected">{{ checkStatus(form.statusPorter) }}</span></td>
                           
    
