            <div class="field" style="padding:0 1rem"><label>approved</label><span style="margin-left:.5rem"><input v-model="filterApproved" style="width:18px;height:18px;" type="checkbox" name="pending" id=""></span></div>
            <div class="field" style="padding:0 1rem"><label>Rejected</label><span style="margin-left:.5rem"><input v-model="filterRejected" style="width:18px;height:18px;" type="checkbox" name="pending" id=""></span></div>
        
            </div>
            
