                        <a href="#"  @click="isActionTaken=false;userPass = '' "><i class='material-icons'>close</i></a> 
                     </div>
                     <h2 style="margin-bottom:2rem;font-size:1.5rem;">Bulk Actions</h2>
                     <div style="overflow-y:scroll;height:5rem;margin-top:1rem;">
