                            <th style="width:1500px !important;">Fullname</th>
                            <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Matric No.</th>
                            
                      
                            <th style="padding:0rem 3rem 0rem 1rem;text-align:left;">Date Sent</th>
                            <th style="padding:0rem 15rem 0rem 1rem;text-align:left;">Description</th>
                            <th style="padding:0rem 5rem 0rem 1rem;text-align:left;">Duration</th>
                    
                            <th>parent's Approval</th>
                            
                            <th>porter's Approval</th>
                            <th>S.A. Approval</th>
                         
                        </tr>
                    </thead>
                    <tbody v-for="form in recentForms" :key="form.id">
                        <tr :id="`pform${form.id}`" v-if="(form.statusStudentAffairs=='0' && filterPending) || (form.statusStudentAffairs=='1' && filterApproved) || (form.statusStudentAffairs=='2' && filterRejected) || (!filterApproved && !filterPending && !filterRejected)">
                            <td  v-if="filterPending"> <input v-if="form.statusStudentAffairs == '0'" type="checkbox" name="" id="" style="width:3rem;height:1.5rem;" @click="checkBulkArray(form.id, form.matric_no)"></td>
