    <!-- ----------SECURITY ------------->

    <div v-if="usercat == 'security'" class="table-data">
        <div class="todo" >
            <div class="head">
                <span style="display:flex;flex-direction: column;"><h3>Search Requests</h3><input type="text" style="height:30px;padding-left:10px;font-size:1rem" placeholder="Enter Form/Request ID"> <span>Search</span></span>
                
            </div>
           
            
          
        </div>

        <div class="order" style="overflow-y: scroll; max-height:40rem;">
            <div class="head" v-if="this.recentForms.length > 1">
                <span><h3>Recent Requests</h3>
                    <router-link to="/history" id="view_all" style="margin-top:0px;"><span>view all</span></router-link>
                
                </span>
                
            </div>
            <div class="head" v-else-if="this.recentForms.length < 1">
                <h3 style="font-size:16px;">No Recent Requests Yet!. <p style="color:#aaa">click the button on the top of the page to create one!</p></h3>
            </div>
            <table>
                <thead>
                    <tr>
                        <th>Form ID</th>
                        <th>Exeat Type</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Date Sent</th>
                        <th style="padding:0rem 10rem 0rem 1rem;text-align:left;">Description</th>
                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Departure Date</th>
                        <th style="padding:0rem 4rem 0rem 1rem;text-align:left;">Arrival Date</th>
                        <th>porter's Approval</th>
                        <th>parent's Approval</th>
                        <th>s.A. Approval</th>
                        <th>Final Remarks</th>
                      
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="form in recentForms" :key="form.id">
                        <td>CULEXT{{ form.id }}</td>
                        <td>{{ form.exeat_type }}</td>
