<script>
import axios from 'axios'


import store from '../store'

import { toast } from 'bulma-toast'

export default {
    name:"History",
    data(){
        return{
            recentForms:[],
          
            username:this.$store.state.username,     
            usercat:this.$store.state.usercat,  
            filterPending:false,
            filterApproved:false,
            filterRejected:false, 
            bulkArray:[],    
            isActionTaken:false,
            userPass:'',
            bulkCommand:'',
        }
        
    },
    mounted(){  
        this.getRecentForms()
        this.timer4 = setInterval(this.checkAuthStat, 1000)
    },
    beforeUnmount(){
        clearInterval(this.timer4)
    },
    methods: {
        checkAuthStat() {

            if (this.$store.state.isAuthenticated === false || this.$store.state.token == '') {
                this.logout()
            }
            },
            async logout() {

            const token = this.$store.state.token
            axios.defaults.headers.common["authorization"] = ""

            localStorage.removeItem("token")
            localStorage.removeItem("username")
            localStorage.removeItem("userid")
            localStorage.removeItem('cT')
            this.$store.commit('removeToken')
            await axios
                .post('api/v1/person/logout/', {
                    token: token
                }, )
                .then(response => {
                    toast({
                        message: `Logged out successfully`,
                        type: 'is-info',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 5000,
                        position: 'top-right',
                    })

                })

            this.$router.push('/login')

            },
        checkBulkArray(pk,mat_no){
            
            if(this.bulkArray.indexOf(JSON.stringify({'id':pk,'matric_no':mat_no}))==-1){
                this.bulkArray.push(JSON.stringify({'id':pk,'matric_no':mat_no}))
            } else {
                this.bulkArray.splice(this.bulkArray.indexOf(JSON.stringify({'id':pk,'matric_no':mat_no})),1)
            }
            

            console.log(this.bulkArray)
        },
        async getRecentForms(){

            if (this.$store.state.isAuthenticated){

                console.table(this.$store.state)
                await axios
                    .get(`/api/v1/recent-forms/${this.$store.state.token}`)
                    .then(response => {
                        //store.commit('setForms',response.data)
                        this.recentForms=response.data
                        

                    })
                    .catch( error => {
                        console.log("and error occured")

                    
                    })
            }
        },
        checkStatus(value){
				if(value=='0'){
					return "Pending"
				}else if(value =='1'){
					return "Approved"
				}
				return "Rejected"
		},
        async bulkTask(){

            const token = this.$store.state.token
            const password = this.userPass
            const command = this.bulkCommand
            const id_list  = []

            for(let x=0;x< this.bulkArray.length;x++){
                id_list.push(JSON.parse(this.bulkArray[x]))
            }

            const data = {
                token:token,
                password:password,
                command:command,
                pk_list:id_list,
            }

            console.table(data)
            if((command != 'approve' && command != 'reject') || this.userPass==''){
                toast({
                    message: `Fill in the Appropriate information`,
                    type: 'is-danger',
                    dismissible: true,
                    pauseOnHover: 'true',
                    duration: 3000,
                    position:'top-center',
                })
                
            } else {
                await axios 
                    .post('/api/v1/actions/bulkTask/', data)
                    .then(response => {
                        console.log("success")
                        if(response.data.message=='completed_successfully'){
                            toast({
                            message: `Running Bulk Task in Background`,
                            type: 'is-info',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'top-center',
                        })
                        this.bulkArray = []
                        this.getRecentForms();
                        
                        }
                        
                    })
                    .catch(error => {
                        console.log('error')
                    })


            }

            this.userPass = ''
            this.bulkCommand = ''

            
            


        },
        async approveRequest(formid, matric_no){
            if(this.$store.state.isAuthenticated){
                this.removeRequest(formid)
            const formData = {
                username:this.$store.state.username,
                token:this.$store.state.token,
                formpk:formid,
                change:"approve",
                matric_no:matric_no,
            }
            await axios
                .post('api/v1/form/change/', formData)
                .then(response => {
                    if(response.data.message=="success"){
                        toast({
                            message: `Form was Approved Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
                    } else {
                        document.getElementById(`pform${formid}`).style.display="";
                        toast({
                            message: `Form Approval Error`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })

                    }

                   
                })
                .catch(error => {
                    console.log('error')
                    document.getElementById(`pform${formid}`).style.display="";
                            toast({
                            message: `Form Approval Error`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
                })
            } else {
                axios.defaults.headers.common["authorization"] = ""

                localStorage.removeItem("token")
                localStorage.removeItem("username")
                localStorage.removeItem("userid")
                localStorage.removeItem('cT')
                this.$store.commit('removeToken')

                await axios
                    .post('/api/v1/person/logout/', {
                        headers: { 
                            "token":`Token ${this.store.state.token}`,

                        }},)
                    .then(response => {
                        toast({
                                message: `${response.data.message}`,
                                type: 'is-success',
                                dismissible: true,
                                pauseOnHover: 'true',
                                duration: 3000,
                                position:'top-right',
                            })
                        
                    })
                
                this.$router.push('/login')
            }
        
        },


        async rejectRequest(formid, matric_no){
            this.removeRequest(formid)
        
            const formData = {
                username:this.$store.state.username,
                token:this.$store.state.token,
                formpk:formid,
                change:"reject",
                matric_no:matric_no,
            }
            await axios
                .post('api/v1/form/change/', formData)
                .then(response => {
                    if(response.data.message=="success"){
                        toast({
                            message: `Form was Rejected Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
                    } else {
                        document.getElementById(`pform${formid}`).style.display="";
                        toast({
                            message: `Form Rejection Error`,
                            type: 'is-danger',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
                
                    }      
                
                })
                .catch(error => {
                    console.log('error')
                    document.getElementById(`pform${formid}`).style.display="";
                    toast({
                        message: `Form Rejection Error`,
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: 'true',
                        duration: 3000,
                        position:'bottom-right',
                    })
                })
                
        
        },

        removeRequest(form_id){
            document.getElementById(`pform${form_id}`).style.display = "none"
        },

        async cancelForm(form_id){
            const data = {
                form_id:form_id,
                token:this.$store.state.token,
                username:this.$store.state.username
            }
            await axios
                .post('/api/v1/form/cancel-form/',data)
                .then(response => {
                    if(response.data == "success"){
                        toast({
                            message: `Form Cancelled Successfully`,
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: 'true',
                            duration: 3000,
                            position:'bottom-right',
                        })
                    }
                })
        },

    }
    
    

}
</script>

